/**
 * author: young
 * createdDate: 2019-07-10
 * description: grid_view
 * @Maintainers: young
 */
BI.GridList = BI.inherit(BI.Widget, {

    props: {
        baseCls: "bi-grid-list",
        items: [],  // 
        // itemFormatter
        itemHeight: 30,
        showLoader: false,
        itemsCreator: BI.emptyFn,
        onLoaded: BI.emptyFn,
        hasNext: BI.emptyFn,
        count: false,
        overscanRowCount: 0,
        overflowX: true,
        overflowY: true
    },

    mounted: function () {
        var self = this;
        this._resizeHandler = BI.debounce(function () {
            var width = self._getListWidth(), height = self._getListHeight();
            if (self.list.getWidth() !== width || self.list.getHeight() !== height) {
                self._populate();
            }
        }, 30);
        this.removeListener = BI.ResizeDetector.addResizeListener(this.wrapper, function () {
            self._resizeHandler();
        });
        if (BI.isNotEmptyArray(this.options.items)) {
            this._populate();
        }
    },

    render: function () {
        var self = this,
            o = this.options;
        this.times = 1;
        this.count = 0;
        this.loaderStatus = "loaded";
        return {
            type: "bi.absolute",
            items: [{
                el: {
                    type: "bi.grid_view",
                    overflowX: o.overflowX,
                    overflowY: o.overflowY,
                    estimatedRowSize: o.itemHeight,
                    estimatedColumnSize: this._getListWidth(),
                    overscanRowCount: o.overscanRowCount,
                    itemFormatter: o.itemFormatter,
                    rowHeightGetter: function () {
                        return o.itemHeight;
                    },
                    columnWidthGetter: function () {
                        return self.listWidth - BI.DOM.getScrollWidth();
                    },
                    ref: function (ref) {
                        self.list = ref;
                    },
                    listeners: [{
                        eventName: BI.GridView.EVENT_SCROLL,
                        action: function (sc) {
                            self.fireEvent("EVENT_SCROLL", sc.scrollTop);
                        }
                    }]
                },
                top: 0,
                right: 0,
                bottom: 0,
                left: 0
            }],
            ref: function (_ref) {
                self.wrapper = _ref;
            }
        };
    },

    _getListWidth: function () {
        return this.element.width();
    },

    _getListHeight: function () {
        return this.element.height();
    },

    _populate: function () {
        var o = this.options,
            self = this;

        this.listWidth = this._getListWidth();
        this.list.setEstimatedColumnSize(this.listWidth - BI.DOM.getScrollWidth());
        this.list.setWidth(this.listWidth);
        this.list.setHeight(this._getListHeight());

        var items = BI.map(this.options.items, function (i, item) {
            return [item];
        });
        if (o.showLoader && (this._hasNext() || this.loaderStatus === "end")) {
            items.push([{
                type: "bi.loading_bar",
                ref: function () {
                    switch (self.loaderStatus) {
                        case "end":
                            this.setEnd();
                            break;
                        case "loaded":
                        default:
                            this.setLoaded();
                            break;
                    }
                },
                listeners: [{
                    eventName: BI.Controller.EVENT_CHANGE,
                    action: function (type) {
                        if (type === BI.Events.CLICK) {
                            this.setLoading();
                            self._nextLoad();
                        }
                    }
                }]
            }]);
        }
        this.list.populate(items);
    },

    _nextLoad: function () {
        var self = this,
            o = this.options;
        o.itemsCreator.apply(this, [{times: ++this.times}, function () {
            self._addItems.apply(self, arguments);
        }]);
    },

    _addItems: function (items) {
        var o = this.options;
        this.count += items.length;
        o.items = BI.concat(o.items, items);
        if (this._hasNext()) {
            this.setLoaded();
        } else {
            this.setEnd();
        }
        this._populate();
    },

    _hasNext: function () {
        var o = this.options;
        if (BI.isNumber(o.count)) {
            return this.count < o.count;
        }
        return !!o.hasNext.apply(this, [{
            times: this.times,
            count: this.count
        }]);
    },

    populate: function (items) {
        var o = this.options,
            self = this;
        if (arguments.length === 0 && (BI.isFunction(o.itemsCreator))) {
            o.itemsCreator.apply(this, [{times: 1}, function () {
                if (arguments.length === 0) {
                    throw new Error("");
                }
                self.populate.apply(self, arguments);
                o.onLoaded();
            }]);
            return;
        }
        if (items && items !== o.items) {
            o.items = items;
        }
        this.times = 1;
        this.count = items.length;
        this.loaderStatus = "loaded";
        this._populate();
    },

    setScrollTop: function (top) {
        this.list.setScrollTop(top);
    },

    setLoaded: function () {
        this.loaderStatus = "loaded";
    },

    setEnd: function () {
        this.loaderStatus = "end";
    },

    setLoading: function () {
        this.loaderStatus = "loading";
    },

    destroyed: function () {
        this.removeListener && this.removeListener();
    }
});
BI.GridList.EVENT_CHANGE = "EVENT_CHANGE";
BI.shortcut("bi.grid_list", BI.GridList);
