import { FormulaInserter } from "./inserter.basic";
import { shortcut } from "../../../core/decorator";

@shortcut()
export class RegularFormulaInserter extends FormulaInserter {
    static xtype = "bi.formula_operating_panel.regular.inserter";
    
    protected getToolbarLayout() {
        return {
            type: BI.AbsoluteLayout.xtype,
            items: [{
                el: {
                    type: BI.Button.xtype,
                    text: BI.i18nText("BI-Basic_Check_Validation"),
                    handler: () => {
                        this.validate();
                    },
                },
                right: 93,
                top: 12,
            }],
        };
    }
}
