import { FormulaFunctionItem } from "./item/item.function";
import { FORMULA_VAR_NODES } from "./formula.constants";
import { FormulaVarItem } from "./item/item.var";

let isCached = false;

let formulaVarItemsCache:{
    text: string;
    value: string;
    type: string;
    def: string;
}[] = [];

let formulaJSONs: {
    def: string;
    name: string;
    type: string;
}[] = [];

/**
 * 
 */
export function isFormulaConfigCached(): boolean {
    return isCached;
}

/**
 * 
 * @param v 
 */
export function getVarDisplayNameByValue(v: string) {
    const varItem = formulaVarItemsCache.find(varItem => varItem.value === v);

    return varItem ? varItem.text : "undefined";
}

/**
 * 
 * @param config 
 */
export function setFormulaConfigCache(config: {
    vars: {
        text: string;
        value: string;
        type: string;
        def: string;
    }[];
    JSONs: {
        def: string;
        name: string;
        type: string;
    }[];
}): void {
    isCached = true;
    formulaVarItemsCache = config.vars;
    BI.FormulaJSONs = config.JSONs;
    BI.FormulaCollections = config.JSONs.reduce((result, json) => {
        result.push(json.name.toLowerCase(), json.name.toUpperCase());

        return result;
    }, [] as string[]);
    formulaJSONs = config.JSONs;
}

/**
 * FunctionTypes
 */
export function transformFormulaJSONs2FunctionTypes(): {
    text: string;
    value: string;
    title: string;
}[] {
    return Object
        .keys(
            formulaJSONs.reduce((resultMap, formulaJSON) => {
                if (!(formulaJSON.type in resultMap)) {
                    resultMap[formulaJSON.type] = [];
                }
        
                return resultMap;
            }, {} as {[key: string]: []})
        )
        .map(type => {
            return {
                text: type,
                title: type,
                value: type,
                cls: "bi-list-item-select",
                textAlign: "left",
                height: 20,
                hgap: 5,
            };
        });
}

/**
 * FunctionNames
 * @param functionType 
 */
export function transformFormulaJSONs2FunctionNames(functionType?: string): {
    text: string;
    value: string;
    title: string;
}[] {
    return Object
        .keys(
            formulaJSONs.reduce((resultMap, formulaJSON) => {
                if (!(formulaJSON.name in resultMap) && (!functionType || formulaJSON.type === functionType)) {
                    resultMap[formulaJSON.name] = [];
                }
        
                return resultMap;
            }, {} as {[key: string]: []})
        )
        .map(type => {
            return {
                type: FormulaFunctionItem.xtype,
                text: type,
                title: type,
                value: type,
                height: 20,
            };
        });
}

/**
 * 
 * @param functionName 
 */
export function getFunctionTypeByFunctionName(functionName: string): string | null {
    const formulaJSON = formulaJSONs
        .find(formulaJSON => formulaJSON.name === functionName);

    if (BI.isNotNull(formulaJSON)) {
        return formulaJSON.type;
    }

    return null;
}

/**
 * 
 * @param functionName 
 */
export function getFunctionDescriptionByFunctionName(functionName: string): string {
    const formulaJSON = formulaJSONs
        .find(formulaJSON => formulaJSON.name === functionName);

    return BI.isNotNull(formulaJSON) ? formulaJSON.def : "";
}

/**
 * functionNames
 * @param keyword 
 */
export function getSearchedFunctionNames(keyword: string): {
    text: string;
    value: string;
    title: string;
}[] {
    if (keyword === "") {
        return [];
    }

    const { match, find }: { match: {
        text: string;
        value: string;
        title: string;
    }[], find: {
        text: string;
        value: string;
        title: string;
    }[] } = BI.Func.getSearchResult(transformFormulaJSONs2FunctionNames(), keyword, "val");

    match.sort((a, b) => a.value > b.value ? 1 : -1);
    find.sort((a, b) => a.value > b.value ? 1 : -1);

    return [...match, ...find];
}

/**
 * 
 */
export function transformFormulaVarItems(): {
    id: string;
    pId: string;
    open?: boolean;
    text: string;
    value: string;
    height: number;
    type: string;
}[] {
    if (!isCached) {
        return [];
    }

    const nodeLeavesMap: {[key: string]: {
        text: string;
        value: string;
        type: string;
    }[]} = {}, height = 25;

    const leaves = formulaVarItemsCache;

    leaves.forEach(leaf => {
        if (leaf.type in nodeLeavesMap) {
            nodeLeavesMap[leaf.type].push(leaf);
        } else {
            nodeLeavesMap[leaf.type] = [leaf];
        }
    });

    const items: {
        id: string;
        pId: string;
        open?: boolean;
        text: string;
        value: string;
        height: number;
        type: string;
    }[] = FORMULA_VAR_NODES.map(node => {
        const size = BI.size(nodeLeavesMap[node.value]);

        return {
            ...node,
            open: true,
            height,
            text: BI.i18nText(node.i18nText) + (size > 0 ? ` - [${size}]` : ""),
        };
    });

    Object.keys(nodeLeavesMap)
        .forEach(nodeType => {
            const node = FORMULA_VAR_NODES.find(node => node.value === nodeType);

            if (!node) {
                return;
            }

            const pId = node.id, nodeLeaves = nodeLeavesMap[nodeType], size = BI.size(nodeLeaves);
            
            items.push(...nodeLeaves.map((leaf, index) => {
                return {
                    pId,
                    id: BI.UUID(),
                    text: leaf.text,
                    value: leaf.value,
                    lgap: 16,
                    layoutType: index === size - 1
                        ? BI.LastTreeLeafItem.xtype
                        : BI.MidTreeLeafItem.xtype,
                    type: FormulaVarItem.xtype,
                    height,
                };
            }));
        });

    return items;
}

/**
 * 
 * @param varName 
 */
export function getVarDescriptionByVarName(varName: string): string {
    const formulaVarItem = formulaVarItemsCache
        .find(formulaVarItem => formulaVarItem.value === varName);

    return BI.isNotNull(formulaVarItem) ? formulaVarItem.def : "";
}

/**
 * 
 * @param str 
 */
export function isBelong2FormulaFunction(str: string): {
    text: string;
    value: string;
    title: string;
} | undefined {
    return transformFormulaJSONs2FunctionNames()
        .find(item => item.text.toLowerCase() === str.toLowerCase());
}
