import { shortcut, store } from "../../core/decorator";
import { transformFormulaJSONs2FunctionTypes, transformFormulaVarItems, isBelong2FormulaFunction, isFormulaConfigCached, setFormulaConfigCache } from "./formula.service";
import { FormulaModel } from "./formula.model";
import { FormulaInserter } from "./inserter/inserter.basic";
import { DEFAULT_FUNCTION_NAME } from "./formula.constants";
import { ButtonGroup, CustomTree, SearchEditor, Text } from '@fui/core';

const searchPaneWidth = 253;
const searchButtonWidth = 54;
const LabelTitleHeight = 24;

@shortcut()
@store(FormulaModel)
export class FormulaOpeartingPanel extends BI.Pane {
    static xtype = "bi.formula_operating_panel";

    private functionTypeGroup: ButtonGroup;

    private functionNameGroup: ButtonGroup;

    private model: FormulaModel["model"];

    private store: FormulaModel["store"];

    private descriptionLabel: Text;

    private searchEditor: SearchEditor;

    private relatedFunctionNameGroup: ButtonGroup;

    private formulaEditor: FormulaInserter;

    private varTree: CustomTree;

    props: {
        inserterType: string;
        value: {
            formula: string;
            [key: string]: any;
        },
        configLoader: (..._args: any[]) => Promise<{
            vars: {
                text: string;
                value: string;
                type: string;
                def: string;
            }[];
            JSONs: {
                def: string;
                name: string;
                type: string;
            }[];
        }>
    } = {
        inserterType: FormulaInserter.xtype,
        value: {
            formula: "",
        },
        configLoader: () => new Promise(resolve => {
            resolve({
                vars: [],
                JSONs: [],
            });
        }),
    };

    watch = {
        functionNames: () => {
            this.functionNameGroup.populate(this.model.functionNames);

            this.scrollFunctionName2View();
        },

        selectedFunctionNameOrVarName: () => {
            if (isBelong2FormulaFunction(this.model.selectedFunctionNameOrVarName)) {
                this.functionNameGroup.setValue(this.model.selectedFunctionNameOrVarName);

                this.scrollFunctionName2View();
            }
        },

        selectedFunctionType: () => {
            this.functionTypeGroup.setValue(this.model.selectedFunctionType);

            this.functionNameGroup.setValue(this.model.selectedFunctionNameOrVarName);
        },

        functionDescription: () => {
            this.descriptionLabel.setText(this.model.functionDescription);
        },

        relatedFunctionNames: () => {
            const relatedFunctionNames = this.model.relatedFunctionNames;

            this.relatedFunctionNameGroup.populate(relatedFunctionNames);

            const relatedFunctionName = BI.isEmptyArray(relatedFunctionNames) ? null : BI.first(relatedFunctionNames).value;

            this.relatedFunctionNameGroup.setValue(relatedFunctionName);

            relatedFunctionName && this.store.setSelectedFunctionName(relatedFunctionName);
        },

        isCached: () => {
            this.functionTypeGroup.populate(transformFormulaJSONs2FunctionTypes());
            this.functionTypeGroup.setValue(this.model.selectedFunctionType);
            this.functionNameGroup.setValue(this.model.selectedFunctionNameOrVarName);
            this.varTree.populate(transformFormulaVarItems());
        },
    }

    render() {
        return {
            type: BI.VTapeLayout.xtype,
            items: [{
                el: {
                    type: BI.LeftRightVerticalAdaptLayout.xtype,
                    items: {
                        left: [{
                            type: BI.Label.xtype,
                            text: BI.i18nText("BI-Basic_Please_Input_Formula"),
                        }],
                        right: [{
                            type: BI.SearchEditor.xtype,
                            ref: (_ref: SearchEditor) => {
                                this.searchEditor = _ref;
                            },
                            width: searchPaneWidth - searchButtonWidth - 5,
                            listeners: [{
                                eventName: BI.SearchEditor.EVENT_ENTER,
                                action: () => {
                                    this.store.setKeyword(this.searchEditor.getValue());
                                },
                            }, {
                                eventName: BI.SearchEditor.EVENT_EMPTY,
                                action: () => {
                                    this.store.setKeyword("");
                                },
                            }],
                        }, {
                            lgap: 5,
                            el: {
                                type: BI.Button.xtype,
                                text: BI.i18nText("BI-Basic_Search"),
                                minWidth: 0,
                                width: searchButtonWidth,
                                handler: () => {
                                    this.store.setKeyword(this.searchEditor.getValue());
                                },
                            },
                        }],
                    },
                },
                height: LabelTitleHeight,
            }, {
                el: {
                    type: BI.HTapeLayout.xtype,
                    items: [{
                        type: this.options.inserterType,
                        ref: (_ref: FormulaInserter) => {
                            this.formulaEditor = _ref;
                        },
                        value: this.options.value,
                        rgap: 10,
                        listeners: [{
                            eventName: "EVENT_SHOW_HINT",
                            action: (hint: string) => {
                                this.store.setSelectedFunctionName(hint);

                                this.scrollFunctionName2View();
                            },
                        }, {
                            eventName: "EVENT_SELECT_HINT",
                            action: (hint: string) => {
                                this.store.setSelectedFunctionName(hint);

                                this.scrollFunctionName2View();
                            },
                        }],
                    }, {
                        el: {
                            type: BI.ButtonGroup.xtype,
                            ref: (_ref: ButtonGroup) => {
                                this.relatedFunctionNameGroup = _ref;
                            },
                            cls: "bi-border",
                            items: this.model.relatedFunctionNames,
                            layouts: [{
                                type: BI.VerticalLayout.xtype,
                            }],
                            listeners: [{
                                eventName: BI.ButtonGroup.EVENT_CHANGE,
                                action: (val: string) => {
                                    this.store.setSelectedFunctionName(val);
                                },
                            }],
                        },
                        width: searchPaneWidth,
                    }],
                },
                height: 200,
                tgap: 10,
            }, {
                type: BI.HTapeLayout.xtype,
                items: [{
                    el: {
                        type: BI.VTapeLayout.xtype,
                        items: [{
                            el: {
                                type: BI.Label.xtype,
                                text: BI.i18nText("BI-Basic_Function_Type"),
                                textAlign: "left",
                            },
                            height: LabelTitleHeight,
                        }, {
                            type: BI.ButtonGroup.xtype,
                            ref: (_ref: ButtonGroup) => {
                                this.functionTypeGroup = _ref;
                            },
                            value: this.model.selectedFunctionType,
                            cls: "bi-border",
                            items: transformFormulaJSONs2FunctionTypes(),
                            layouts: [{
                                type: BI.VerticalLayout.xtype,
                            }],
                            listeners: [{
                                eventName: BI.ButtonGroup.EVENT_CHANGE,
                                action: (val: string) => {
                                    this.store.setSelectedFunctionType(val);
                                },
                            }],
                        }],
                    },
                    width: 135,
                }, {
                    el: {
                        type: BI.VTapeLayout.xtype,
                        items: [{
                            el: {
                                type: BI.Label.xtype,
                                text: BI.i18nText("BI-Basic_Function_Name"),
                                textAlign: "left",
                            },
                            height: LabelTitleHeight,
                        }, {
                            type: BI.ButtonGroup.xtype,
                            ref: (_ref: ButtonGroup) => {
                                this.functionNameGroup = _ref;
                            },
                            value: this.model.selectedFunctionNameOrVarName,
                            cls: "bi-border",
                            items: this.model.functionNames,
                            layouts: [{
                                type: BI.VerticalLayout.xtype,
                            }],
                            listeners: [{
                                eventName: BI.Controller.EVENT_CHANGE,
                                action: (_type: string, val: string, _widget: any, e: any) => {
                                    this.store.setSelectedFunctionName(val, false);

                                    if (e.type === "dblclick") {
                                        this.formulaEditor.insertFunction(val);
                                    }
                                },
                            }],
                        }],
                    },
                    lgap: 5,
                    width: 135,
                }, {
                    el: {
                        type: BI.VTapeLayout.xtype,
                        items: [{
                            el: {
                                type: BI.Label.xtype,
                                text: BI.i18nText("BI-Basic_Vars"),
                                textAlign: "left",
                            },
                            height: LabelTitleHeight,
                        }, {
                            type: BI.CustomTree.xtype,
                            cls: "bi-border",
                            chooseType: BI.Selection.Single,
                            ref: (_ref: CustomTree) => {
                                this.varTree = _ref;
                            },
                            el: {
                                type: BI.ButtonTree.xtype,
                                layouts: [{
                                    type: BI.VerticalLayout.xtype,
                                }],
                            },
                            items: transformFormulaVarItems(),
                            listeners: [{
                                eventName: BI.Controller.EVENT_CHANGE,
                                action: (type: string, val: string, _widget: any, e: any) => {
                                    if (type !== BI.Events.CLICK) {
                                        return;
                                    }

                                    this.store.setSelectedVarName(val);

                                    if (e && e.type === "dblclick") {
                                        this.formulaEditor.insertField(val);
                                    }
                                },
                            }],
                        }],
                    },
                    lgap: 5,
                    width: 300,
                }, {
                    el: {
                        type: BI.VTapeLayout.xtype,
                        items: [{
                            el: {
                                type: BI.Label.xtype,
                                text: BI.i18nText("BI-Basic_Function_Description"),
                                textAlign: "left",
                            },
                            height: LabelTitleHeight,
                        }, {
                            type: BI.VerticalLayout.xtype,
                            cls: "bi-border",
                            items: [{
                                el: {
                                    type: BI.Text.xtype,
                                    ref: (_ref: Text) => {
                                        this.descriptionLabel = _ref;
                                    },
                                    text: this.model.functionDescription,
                                    whiteSpace: "normal",
                                    hgap: 5,
                                },
                            }],
                        }],
                    },
                    lgap: 5,
                }],
            }],
        };
    }

    beforeRender(callback: Function) {
        if (isFormulaConfigCached()) {
            callback();

            return;
        }

        this.loading();
        this.options.configLoader()
            .then(data => {
                this.loaded();
                setFormulaConfigCache(data);

                this.store.setSelectedFunctionName(DEFAULT_FUNCTION_NAME);

                callback();
            });
    }

    /**
     * functionName
     */
    private scrollFunctionName2View(): void {
        if (!this.model.needScrollToView) {
            return;
        }

        const functionNameWidget = this.functionNameGroup.getAllButtons<any>()
            .find((functionNameWidget: any) => functionNameWidget.getValue() === this.model.selectedFunctionNameOrVarName);

        if (BI.isNull(functionNameWidget)) {
            return;
        }

        functionNameWidget.element[0].scrollIntoView();
    }

    getValue() {
        return this.formulaEditor.getValue();
    }

    /**
     * 
     * @param data 
     */
    loadConfigData(data: {
        vars: {
            text: string;
            value: string;
            type: string;
            def: string;
        }[];
        JSONs: {
            def: string;
            name: string;
            type: string;
        }[];
    }) {
        if (isFormulaConfigCached()) {
            return;
        }

        setFormulaConfigCache(data);

        this.store.setSelectedFunctionName(DEFAULT_FUNCTION_NAME);
    }
}
