import "./stickytable.row";
import {StickyTableLayout} from "./stickytable.layout";
import "./stickytable.header";

/**
 * StickyTable
 *
 * Created by GUY on 2022/9/24.
 * @class StickyTable
 * @extends BI.Widget
 */
var FreezeTable = BI.inherit(BI.Widget, {
    _defaultConfig: function () {
        return BI.extend(FreezeTable.superclass._defaultConfig.apply(this, arguments), {
            baseCls: "bi-sticky-table",
            headerRowSize: 30,
            rowSize: 30,

            columnSize: [],
            minColumnSize: [],
            maxColumnSize: [],

            freezeCols: [0],
            scrollLeft: 0,
            scrollTop: 0,

            sortable: true,
            checkable: true,
            bordered: false,
            stripe: false,
            hover: false,

            header: [],
            items: [],
            itemsCreator: BI.emptyFn,
        });
    },

    // attr: function (key, value) {
    //     var v = FreezeTable.superclass.attr.apply(this, arguments);
    //     if (!value) {
    //         return v;
    //     }
    //     return this.tableColletion.attr.apply(this.tableColletion, arguments);
    // },

    render: function () {
        var self = this, o = this.options;

        this.headerColletion = BI.createWidget({
            type: "bi.sticky_table_header",
            css: {
                overflowY: "scroll",
                overflowX: "hidden"
            },
            columnSize: o.columnSize,
            minColumnSize: o.minColumnSize,
            maxColumnSize: o.maxColumnSize,
            freezeCols: o.freezeCols.slice(),
            header: o.header,
            headerRowSize: o.headerRowSize
        });
        this.bottomRightCollection = BI.createWidget({
            type: "bi.virtual_tree_list",
            css: {
                overflowY: "scroll",
                overflowX: "auto"
            },
            items: this._formatItems(o.items),
            itemsCreator: (op, callback) => {
                o.itemsCreator(op, (items) => {
                    callback(this._formatItems(items));
                });
            },
            el: {
                type: 'bi.virtual_group_list',
                configRender: function (render) {
                    return {
                        ...render,
                        scrolly: false
                    };
                },
                listeners: [{
                    eventName: "EVENT_CHANGE",
                    action: function () {
                        self.fireEvent("EVENT_CHANGE", arguments);
                    }
                }],
                rowHeight: o.rowSize,
                el: {
                    type: "bi.sticky_table_layout",
                    ref: (_ref) => {
                        this.tableColletion = _ref;
                    },
                    mirror: {
                        appendTo: this.element[0]
                    },
                    columnSize: o.columnSize,
                    minColumnSize: o.minColumnSize,
                    maxColumnSize: o.maxColumnSize,
                    isNeedFreeze: o.isNeedFreeze,
                    freezeCols: o.freezeCols.slice(),
                    sortable: o.sortable,
                    checkable: o.checkable,
                    listeners: {
                        [StickyTableLayout.EVENT_STOP_DRAG]: (move) => {
                            BI.nextTick(() => {
                                this.bottomRightCollection.move(move[0], move[1]);
                                this.fireEvent("EVENT_STOP_DRAG", this.bottomRightCollection.getTree(), move[0], move[1]);
                                this.fireEvent("EVENT_SORT", this.bottomRightCollection.getTree(), move[0], move[1]);
                            })
                        },
                        [StickyTableLayout.EVENT_START_DRAG]: (sourceId) => {
                            o.scrollLeft = 0;
                            this._scrollHandler();
                            this.bottomRightCollection.element.scrollLeft(0);
                            this.headerColletion.element.scrollLeft(0);
                            this.fireEvent("EVENT_START_DRAG", this.bottomRightCollection.getTree(), sourceId);
                        }
                    },
                }
            }
        });
        this.contextLayout = BI.createWidget({
            type: "bi.vertical_fill",
            rowSize: ["", "fill"],
            cls: `${o.bordered ? "bordered" : ""} ${o.stripe ? "stripe" : ""} ${o.hover ? "hover" : ""}`,
            element: this,
            items: [{
                el: this.headerColletion,
            }, {
                el: this.bottomRightCollection
            }]
        });

    },

    _scrollHandler: function () {
        var o = this.options;
        if (o.scrollLeft > 0) {
            this.element.addClass("fixed-left-scroll");
        } else {
            this.element.removeClass("fixed-left-scroll");
        }
        var containerWidth = this.tableColletion.table.element.width();
        var clientWidth = this.element.width();
        if (containerWidth - (o.scrollLeft + clientWidth - BI.GridTableScrollbar.SIZE) < 1) {
            this.element.removeClass("fixed-right-scroll");
        } else {
            this.element.addClass("fixed-right-scroll");
        }
    },

    _freeHandler: function () {
        // 
        if (this._hasOtherFreezeCol()) {
            const realColumnSize = this.headerColletion.getRealColumnSize();
            this.options.freezeCols.forEach((col) => {
                const firstRowLength = this.options.header[0].length;
                if (col !== 0 && col !== firstRowLength - 1) {
                    this.element[0].style.setProperty(`--index-${col}-left`, `${realColumnSize.slice(0, col).reduce((item, sum) => sum + item, 0)}px`);
                }
            });
        }
    },

    _handleResize: function () {
        this.options.scrollLeft = this.bottomRightCollection.element.scrollLeft();
        this._scrollHandler();
        this._freeHandler();
    },

    _hasOtherFreezeCol: function () {
        const o = this.options;
        return BI.some(o.freezeCols, (i, col) => {
            const firstRowLength = o.header[0].length;
            if (col !== 0 && col !== firstRowLength - 1) {
                return true;
            }
        });
    },

    mounted: function () {
        var o = this.options;
        if (o.header.length > 0 || o.items.length > 0) {
            this._digest();
            this._populate();
        }
        this._scrollHandler();
        this._freeHandler();
        this.bottomRightCollection.element.scroll(() => {
            o.scrollTop = this.bottomRightCollection.element.scrollTop();
            o.scrollLeft = this.bottomRightCollection.element.scrollLeft();
            this._scrollHandler();
            this.headerColletion.element.scrollLeft(o.scrollLeft);
        });
        this.resizeObserver = BI.ResizeDetector.addResizeListener(this, this._handleResize.bind(this));

        // this.headerColletion.element.scroll(() => {
        //     o.scrollLeft = this.headerColletion.element.scrollLeft();
        //     this.bottomRightCollection.element.scrollLeft(o.scrollLeft);
        // });
    },

    destroyed: function () {
        this.resizeObserver();
    },

    _populateTable: function () {
        var self = this, o = this.options;
        this.headerColletion.populate(o.header);
        this.bottomRightCollection.populate(this._formatItems(o.items));
    },

    _formatItems: function (items) {
        return items.map(({ items, id, pId, layer, isParent, draggable, checkable, open, prevComponent, ...others }) => {
            return {
                type: "bi.sticky_table_row",
                id,
                pId,
                layer,
                isParent,
                draggable,
                checkable,
                prevComponent,
                open,
                height: this.options.rowSize,
                dimension: others,
                items,
            };
        });
    },

    _digest: function () {
        var o = this.options;
        // var freezeColLength = this._getFreezeColLength();
        // 
        // if (this._getFreezeHeaderHeight() <= 0) {
        //     this.topLeftItems = [];
        //     this.topRightItems = [];
        //     this.bottomLeftItems = BI.CollectionTable.serialize(this._getActualItems(), 0, freezeColLength, o.rowSize, o.columnSize, o.mergeCols, BI.range(o.header.length), o.mergeRule);
        //     this.bottomRightItems = BI.CollectionTable.serialize(this._getActualItems(), freezeColLength, o.columnSize.length, o.rowSize, o.columnSize, o.mergeCols, BI.range(o.header.length), o.mergeRule);
        // } else {
        // this.topRightItems = BI.CollectionTable.serialize(o.header, freezeColLength, o.columnSize.length, o.headerRowSize, o.columnSize, true, [], o.mergeRule);
        // this.bottomLeftItems = BI.CollectionTable.serialize(o.items, 0, freezeColLength, o.rowSize, o.columnSize, o.mergeCols, [], o.mergeRule);
        // this.bottomRightItems = BI.CollectionTable.serialize(o.items, 0, o.columnSize.length, o.rowSize, o.columnSize, o.mergeCols, [], o.mergeRule);
        // }
    },

    _populate: function () {
        this._populateTable();
    },

    setVerticalScroll: function (scrollTop) {
        return this.bottomRightCollection.scrollTo(scrollTop);
    },

    setLeftHorizontalScroll: function (scrollLeft) {
    },

    setRightHorizontalScroll: function (scrollLeft) {
    },

    getVerticalScroll: function () {
        return this.options.scrollTop;
    },

    getRightHorizontalScroll: function () {
        return this.options.scrollLeft;
    },

    setWidth: function (width) {
        FreezeTable.superclass.setWidth.apply(this, arguments);
        this._width = this.options.width - BI.GridTableScrollbar.SIZE;
    },

    setHeight: function (height) {
        FreezeTable.superclass.setHeight.apply(this, arguments);
        this._height = this.options.height - BI.GridTableScrollbar.SIZE;
    },

    setColumnSize: function (columnSize) {
        const o = this.options;
        this._isNeedDigest = true;
        this.options.columnSize = columnSize;
        this.headerColletion.setColumnSize(columnSize);
        this.tableColletion.setColumnSize(columnSize);
        this._scrollHandler();
        this._freeHandler();
    },

    getColumnSize: function () {
        return this.options.columnSize;
    },

    getRegionSize: function () {
        return 0;
    },

    getRegionColumnSize: function () {
        return [];
    },

    setRegionColumnSize: function () {

    },

    getValue: function () {
        return this.bottomRightCollection.getValue();
    },

    populate: function (items, header) {
        if (items && items !== this.options.items) {
            this._isNeedDigest = true;
            this.options.items = items;
            this._restore();
        }
        if (header && header !== this.options.header) {
            this._isNeedDigest = true;
            this.options.header = header;
            this._restore();
        }
        this._populate();
    },

    _restore: function () {
    },

    restore: function () {
        this._restore();
    }
});
BI.shortcut("bi.freeze_table", FreezeTable);

var Table = BI.TableComponnet = BI.inherit(BI.Widget, {
    _defaultConfig: function () {
        return BI.extend(Table.superclass._defaultConfig.apply(this, arguments), {
            freezeCols: [], // ,0

            isNeedMerge: false,
            mergeRule: BI.emptyFn,

            columnSize: [],
            minColumnSize: [],
            maxColumnSize: [],

            headerRowSize: 30,
            rowSize: 30,

            sortable: true,
            resizable: true, // API
            checkable: true,
            bordered: false,
            stripe: true,
            hover: true,

            header: [], // 
            items: [],
            itemsCreator: BI.emptyFn,
        });
    },

    attr: function (key, value) {
        var v = Table.superclass.attr.apply(this, arguments);
        if (BI.isNull(value)) {
            return v;
        }
        return this.table.attr.apply(this.table, arguments);
    },

    render: function () {
        var o = this.options;
        if (o.items[0] && o.items[0].length > 0) {
            return {
                type: "bi.resizable_table",
                ref: (_ref) => {
                    this.table = _ref;
                },
                el: {
                    type: "bi.table_view",
                    bordered: o.bordered,
                    stripe: o.stripe,
                    hover: o.hover
                },
                width: o.width,
                height: o.height,
                isNeedFreeze: true,
                freezeCols: o.freezeCols,
                isNeedResize: o.isNeedResize || o.resizable,
                minColumnSize: o.minColumnSize,
                maxColumnSize: o.maxColumnSize,
                columnSize: o.columnSize,
                headerRowSize: o.headerRowSize,
                rowSize: o.rowSize,
                isNeedMerge: o.isNeedMerge,
                mergeRule: o.mergeRule,
                header: o.header,
                items: o.items
            };
        }
        return {
            type: "bi.resizable_table",
            ref: (_ref) => {
                this.table = _ref;
            },
            el: {
                type: "bi.freeze_table",
                ref: (_ref) => {
                    this.innerTable = _ref;
                },
                listeners: [{
                    eventName: "EVENT_SORT",
                    action: (...args) => {
                        this.fireEvent("EVENT_SORT", ...args);
                    }
                }, {
                    eventName: "EVENT_START_DRAG",
                    action: (...args) => {
                        this.fireEvent("EVENT_START_DRAG", ...args);
                    }
                }, {
                    eventName: "EVENT_STOP_DRAG",
                    action: (...args) => {
                        this.fireEvent("EVENT_STOP_DRAG", ...args);
                    }
                }, {
                    eventName: "EVENT_CHANGE",
                    action: (...args) => {
                        this.fireEvent("EVENT_CHANGE", ...args);
                    }
                }],
                freezeCols: o.freezeCols,
                itemsCreator: o.itemsCreator,
                sortable: o.sortable,
                checkable: o.checkable,
                bordered: o.bordered,
                stripe: o.stripe,
                hover: o.hover
            },
            width: o.width,
            height: o.height,
            isNeedResize: o.isNeedResize || o.resizable,
            minColumnSize: o.minColumnSize,
            maxColumnSize: o.maxColumnSize,
            columnSize: o.columnSize,
            headerRowSize: o.headerRowSize,
            rowSize: o.rowSize,
            header: o.header,
            items: o.items
        };
    },

    setVerticalScroll: function (scrollTop) {
        return this.table.setVerticalScroll(scrollTop);
    },

    setLeftHorizontalScroll: function (scrollLeft) {
    },

    setRightHorizontalScroll: function (scrollLeft) {
    },

    getVerticalScroll: function () {
        return this.table.getVerticalScroll();
    },

    getRightHorizontalScroll: function () {
        return this.table.getRightHorizontalScroll();
    },

    setWidth: function (width) {
        Table.superclass.setWidth.apply(this, arguments);
        this.table.setWidth(width);
    },

    setHeight: function (height) {
        Table.superclass.setHeight.apply(this, arguments);
        this.table.setHeight(height);
    },

    setColumnSize: function (columnSize) {
        this.table.setColumnSize(columnSize);
    },

    getColumnSize: function () {
        return this.options.columnSize;
    },

    populate: function (items, header) {
        this.table.populate(items, header);
    },

    getValue: function () {
        return this.innerTable.getValue();
    },

    restore: function () {
        this.table.restore();
    }
});
BI.shortcut("bi.components.table", Table);
