import { shortcut, store } from "../../../core/decorator";
import { inputTypes } from "../parameter";
import "./input/input";
import { getInput } from "./input/input.service";
import "./parameter_input.less";
import { ParameterInputModel } from "./parameter_input.model";
const tipType = "tip";
@shortcut()
@store(ParameterInputModel)
export class ParameterInput extends BI.BasicButton {
    static xtype = "bi.parameter_editor.parameter_input"

    props = {
        inputName: "",
        inputValue: "",
        inputType: "String",
        height: 22,
        id: "",
        baseCls: "parameter-editor-input",
        value: "",
    }

    store: ParameterInputModel["store"]
    model: ParameterInputModel["model"]

    tab: any;
    parameterName: any;
    parameterValue: any;
    iconCombo: any;

    render() {
        let { inputName, inputValue } = this.options;
        const { inputType } = this.options;
        const { id } = this.options;

        return {
            type: BI.CenterLayout.xtype,
            cls: "bi-border",
            items: [{
                type: BI.Editor.xtype,
                height: 22,
                allowBlank: true,
                value: inputName,
                ref: (_ref: any) => {
                    this.parameterName = _ref;
                },
                listeners: [{
                    eventName: BI.TextEditor.EVENT_CHANGE,
                    action: () => {
                        inputName = this.parameterName.getValue();
                        this.tab.setSelect(inputName ? inputType : tipType);
                        inputValue = inputName ? inputValue : "";
                        this.store.setParameterName(id, inputName);
                        this.fireEvent(BI.TextEditor.EVENT_CHANGE);
                    },
                }, {
                    eventName: BI.TextEditor.EVENT_FOCUS,
                    action: () => {
                        this.store.setSelectedId(id);
                    },
                }],
            }, {
                type: BI.HTapeLayout.xtype,
                items: [{
                    type: BI.Tab.xtype,
                    single: true,
                    showIndex: inputName ? inputType : tipType,
                    ref: (_ref: any) => {
                        this.tab = _ref;
                    },
                    cardCreator: (index: inputTypes | "tip") => this.renderInputs(index),
                }],
            }],
        };
    }

    private renderInputs(type: inputTypes | "tip") {
        const { inputValue, id } = this.options;
        if (type === "tip") {
            return {
                type: BI.TextButton.xtype,
                cls: "bi-error bi-border-left",
                text: BI.i18nText("BI-Basic_Parameter_Please_Set_Parameter_Name"),
                handler: () => {
                    this.parameterName.focus();
                },
            };
        }
        const xtype = getInput(type);

        return {
            type: BI.HTapeLayout.xtype,
            items: [{
                el: {
                    type: BI.IconCombo.xtype,
                    cls: "bi-border-left bi-border-right",
                    height: 22,
                    width: 25,
                    value: type,
                    items: this.renderDownList(),
                    ref: (_ref: any) => {
                        this.iconCombo = _ref;
                    },
                    listeners: [{
                        eventName: "EVENT_CHANGE",
                        action: () => {
                            this.tab.setSelect(tipType);
                            const typeValue = this.iconCombo.getValue()[0];
                            if (typeValue) {
                                this.tab.setSelect(typeValue);
                            }
                            this.store.setParameterValue(id, "", typeValue);
                            this.fireEvent('EVENT_CHANGE');
                        },
                    }],
                },
                width: 25,
            }, {
                type: xtype,
                value: inputValue,
                listeners: [{
                    eventName: "EVENT_CHANGE",
                    action: (value: string) => {
                        const type = this.iconCombo.getValue()[0];
                        this.store.setParameterValue(id, value, type);
                        this.fireEvent("EVENT_CHANGE");
                    },
                }, {
                    eventName: "EVENT_FOCUS",
                    action: () => {
                        this.store.setSelectedId(id);
                    },
                }],
            }],
        };
    }

    private renderDownList() {
        return [{
            text: BI.i18nText("BI-Basic_Parameter_Type_String"),
            value: "String",
            iconCls: "parameter-string-font",
        }, {
            text: BI.i18nText("BI-Basic_Parameter_Type_Int"),
            value: "Integer",
            iconCls: "parameter-int-font",
        }, {
            text: BI.i18nText("BI-Basic_Parameter_Type_Number"),
            value: "Double",
            iconCls: "parameter-double-font",
        }, {
            text: BI.i18nText("BI-Basic_Parameter_Type_Date"),
            value: "Date",
            iconCls: "parameter-date-font",
        }, {
            text: BI.i18nText("BI-Basic_Parameter_Type_Boolean"),
            value: "Boolean",
            iconCls: "parameter-boolean-font",
        }, {
            text: BI.i18nText("BI-Basic_Parameter_Type_Formula"),
            value: "Formula",
            iconCls: "parameter-formula-font",
        }];
    }
}
