import { allTokens } from "./token";
import { Lexer } from "chevrotain";
import getFormulaParser from "./FormulaParser";
import getFormulaInterpreter from "./FormulaInterpreter";
import FunctionCheck from "./FunctionCheck";
import get from "lodash/get";

/**
 * 
 */
class FormulaCheck {
  setFormula() {}

  constructor(checker) {
    this.checker = checker;
    this.lexer = new Lexer(allTokens, {
      ensureOptimizations: true
    });
    this.parser = getFormulaParser();
    const { interpreter, setFormula } = getFormulaInterpreter();
    this.interpreter = interpreter;
    this.setFormula = setFormula;
  }

  parse(input) {
    // 
    this.setFormula(new FunctionCheck(this.checker));
    // tokens
    const lexResult = this.lexer.tokenize(input);
    if (lexResult.errors.length > 0) {
      const error = lexResult.errors[0];
      throw new Error(
        JSON.stringify({
          type: "lexer",
          name: error.message,
          token: get(error.message.match(/->(.)<-/), [1], ""),
          errorText: BI.i18nText('BI-Expression_syntax_error'),
        })
      );
    }
    // tokensparser
    this.parser.input = lexResult.tokens;
    const cst = this.parser.expr();
    if (this.parser.errors.length > 0) {
      const error = this.parser.errors[0];
      throw new Error(
        JSON.stringify({
          type: "parser",
          name: error.name,
          token: get(
            error.message.match(/->\s(\w+)\s<-/),
            [1],
            get(error, "previousToken.image") || get(error, "token.image")
          ),
          errorText: BI.i18nText('BI-Expression_syntax_error'),
        })
      );
    }
    // 
    return this.interpreter.visit(cst);
  }
}

export default FormulaCheck;
