/**
 * ------------------------------------------------------------------
 * 
 * @param    {array}  value     
 * @param    {array}  colors    
 * ------------------------------------------------------------------
 */
!(function () {
    var SelectColorItem = BI.inherit(BI.BasicButton, {

        props: {
            extraCls: "bi-select-color-item bi-border",
            value: [],
            colors: [],
            once: true,
            height: 50,
            width: 130
        },

        mounted: function () {
            var o = this.options;
            BI.each(this.layouts, function (i, layout) {
                layout.element.css("background-color", o.value[i]);
            });
        },

        render: function () {
            var self = this, o = this.options;
            this.layouts = [];
            return {
                type: "bi.absolute",
                items: [{
                    el: {
                        type: "bi.button_group",
                        items: BI.map(o.colors, function () {
                            return {
                                type: "bi.layout",
                                ref: function (ref) {
                                    self.layouts.push(ref);
                                }
                            };
                        }),
                        layouts: [{
                            type: "bi.center",
                            hgap: 0.5,
                            vgap: 2
                        }]
                    },
                    left: 1,
                    right: 2,
                    top: 0,
                    bottom: 0
                }]
            };
        }
    });
    SelectColorItem.EVENT_CHANGE = "EVENT_CHANGE";
    BI.shortcut("bi.select_color_item", SelectColorItem);
})();