import { CstParser } from "chevrotain";
import { token, allTokens } from "./token";

/**
 * 
 */
class FormulaParser extends CstParser {
  expr = this.RULE("expr", () => {
    this.SUBRULE(this.commaExpression);
  });

  commaExpression = this.RULE("commaExpression", () => {
    this.SUBRULE(this.binaryExpression);
    this.MANY(() => {
      this.CONSUME(token.Comma);
      this.SUBRULE2(this.binaryExpression);
    });
  });

  binaryExpression = this.RULE("binaryExpression", () => {
    this.SUBRULE(this.unaryExpression);
    this.MANY(() => {
      this.OR(
        this.c1 ||
          (this.c1 = [
            {
              ALT: () => {
                this.CONSUME(token.LogicOr);
              }
            },
            {
              ALT: () => {
                this.CONSUME(token.LogicAnd);
              }
            },
            // {
            //   ALT: () => {
            //     this.CONSUME(token.BitOr);
            //   }
            // },
            {
              ALT: () => {
                this.CONSUME(token.BitAnd);
              }
            },
            {
              ALT: () => {
                this.CONSUME(token.Equal);
              }
            },
            {
              ALT: () => {
                this.CONSUME(token.Compare);
              }
            },
            {
              ALT: () => {
                this.CONSUME(token.Addition);
              }
            },
            {
              ALT: () => {
                this.CONSUME(token.Multiplication);
              }
            }
          ])
      );
      this.SUBRULE2(this.unaryExpression);
    });
  });

  unaryExpression = this.RULE("unaryExpression", () => {
    this.MANY(() => {
      this.CONSUME(token.Unary);
    });
    this.SUBRULE(this.atomicExpression);
  });

  atomicExpression = this.RULE("atomicExpression", () => {
    this.OR(
      this.c2 ||
        (this.c2 = [
          {
            ALT: () => {
              this.SUBRULE(this.array);
            }
          },
          {
            ALT: () => {
              this.SUBRULE(this.func);
            }
          },
          {
            ALT: () => {
              this.SUBRULE(this.group);
            }
          },
          {
            ALT: () => {
              this.SUBRULE(this.base);
            }
          }
        ])
    );
    this.MANY(() => {
      this.CONSUME(token.LSquare);
      this.SUBRULE(this.expr);
      this.CONSUME(token.RSquare);
    });
  });

  array = this.RULE("array", () => {
    this.CONSUME(token.LSquare);
    this.MANY_SEP({
      SEP: token.Comma,
      DEF: () => {
        this.SUBRULE(this.binaryExpression);
      }
    });
    this.CONSUME(token.RSquare);
  });

  func = this.RULE("func", () => {
    this.CONSUME(token.Func);
    this.CONSUME(token.LParen);
    this.OPTION(() => {
      // this.SUBRULE(this.binaryExpression);
      // this.MANY(() => {
      //   this.CONSUME(token.Comma);
      //   this.SUBRULE2(this.binaryExpression);
      // });
      // this.OPTION2(() => {
      //   this.CONSUME2(token.Comma);
      // });
      // Func(1,)MANY_SEP
        this.MANY_SEP({
            SEP: token.Comma,
            DEF: () => {
                this.SUBRULE(this.binaryExpression);
            },
        });
    });
    this.CONSUME(token.RParen);
  });

  group = this.RULE("group", () => {
    this.CONSUME(token.LParen);
    this.AT_LEAST_ONE_SEP({
      SEP: token.Comma,
      DEF: () => {
        this.SUBRULE(this.expr);
      }
    });
    this.CONSUME(token.RParen);
  });

  base = this.RULE("base", () => {
    this.OR(
      this.c3 ||
        (this.c3 = [
          {
            ALT: () => {
              this.CONSUME(token.String);
            }
          },
          {
            ALT: () => {
              this.CONSUME(token.Number);
            }
          },
            {
                ALT: () => {
                    this.CONSUME(token.Date);
                }
            },
          {
            ALT: () => {
              this.CONSUME(token.Boolean);
            }
          },
          {
            ALT: () => {
              this.CONSUME(token.Empty);
            }
          },
          {
            ALT: () => {
              this.CONSUME(token.NaN);
            }
          }
        ])
    );
  });

  constructor() {
    super(allTokens);
    this.performSelfAnalysis();
  }
}

/**
 * parser
 */
const getFormulaParser = () => {
  return new FormulaParser();
};

export { FormulaParser };
export default getFormulaParser;
