!(function () {
    var Item = BI.inherit(BI.Widget, {

        props: {
            baseCls: "bi-form-item",
            stop: true,
            rules: {}
        },


        render: function () {
            var o = this.options;
            if (o.label) {
                this.item = BI.createWidget({
                    rules: o.rules,
                    stop: o.stop
                }, o.el);
                return {
                    type: "bi.vertical_adapt",
                    items: [{
                        el: BI.isKey(o.label) ? {
                            type: "bi.label",
                            textAlign: "left",
                            textHeight: 24,
                            text: o.label,
                            title: o.label,
                            rgap: 10
                        } : o.label
                    }, {
                        el: this.item
                    }]
                }
            }
            this.item = BI.createWidget({
                rules: o.rules,
                stop: o.stop,
                element: this
            }, o.el);
        },

        getValue: function () {
            return this.item.getValue();
        },

        validate: function () {
            return BI.isFunction(this.item.validate) ? this.item.validate() : this._validate();
        },

        getValidations: function () {
            var o = this.options;
            return BI.isFunction(this.item.getValidations)
                ? this.item.getValidations() : BI.validator.validate(this.getValue(), o.rules, o.stop);
        },

        showError: function (text) {
            BI.isFunction(this.item.showError) && this.item.showError(text);
        },

        hideError: function () {
            BI.isFunction(this.item.hideError) && this.item.hideError();
        },

        _validate: function () {
            var o = this.options;
            var res = BI.validator.validate(this.getValue(), o.rules, o.stop);
            return BI.isNull(res) || res.approved;
        }
    });
    BI.shortcut("bi.form.item", Item);
})();