import { shortcut } from "../../../../core/decorator";
import { input } from "./input.service";

@shortcut()
@input("Double")
export class InputDouble extends BI.Widget {
    static xtype = "bi.parameter_editor.parameter_input.double"

    props = {
        value: "",
    }

    input: any;
    
    render() {
        const { value } = this.options;
        
        return {
            type: BI.Editor.xtype,
            height: 22,
            allowBlank: true,
            value,
            ref: (_ref: any) => {
                this.input = _ref;
            },
            validationChecker: (v: string) => BI.isNumeric(v),
            listeners: [{
                eventName: BI.Editor.EVENT_CHANGE,
                action: () => {
                    const value = this.input.getValue();
                    this.fireEvent("EVENT_CHANGE", value);
                },
            }, {
                eventName: BI.Editor.EVENT_FOCUS,
                action: () => {
                    this.fireEvent("EVENT_FOCUS");
                },
            }],
        };
    }
}
