import { shortcut } from "../../../../core/decorator";
import { input } from "./input.service";

@shortcut()
@input("Boolean")
export class InputBoolean extends BI.BasicButton {
    static xtype = "bi.parameter_editor.parameter_input.boolean"

    props = {
        value: "",
    }

    input: any;
    hasSelected: boolean;
    
    render() {
        const { value } = this.options;
        this.hasSelected = value === "true";
        
        return {
            type: BI.MultiSelectItem.xtype,
            width: 80,
            selected: this.hasSelected,
            text: "true",
            ref: (_ref: any) => {
                this.input = _ref;
            },
            listeners: [{
                eventName: BI.MultiSelectItem.EVENT_CHANGE,
                action: () => {
                    this.hasSelected = !this.hasSelected;
                    this.fireEvent("EVENT_CHANGE", this.hasSelected);
                },
            }],
        };
    }

    doClick() {
        this.fireEvent("EVENT_FOCUS");
    }
}
