import { Draggable } from '@shopify/draggable';

/**
 * @author guy
 * @version 2.0
 * Created by guy on 2022/9/25
 */
const StickyTableLayout = BI.inherit(BI.Widget, {
    props: {
        baseCls: 'bi-sticky-table-layout',
        sortable: true,
        checkable: true,
        columnSize: [],
        minColumnSize: [],
        maxColumnSize: [],
        freezeCols: [],
        mirror: {}
    },

    render: function () {
        var self = this, o = this.options;

        return {
            type: "bi.default",
            items: [{
                type: 'bi.default',
                tagName: "table",
                ref: (_ref) => {
                    this.table = _ref;
                },
                width: BI.sum(o.columnSize),
                cls: "border-sizing",
                css: {
                    border: 0,
                    borderSpacing: 0,
                    borderCollapse: "collapse",
                    tableLayout: "fixed",
                    minWidth: "100%"
                },
                items: [{
                    type: "bi.default",
                    ref: (_ref) => {
                        this.colgroup = _ref;
                    },
                    tagName: "colgroup",
                    items: this._formatCol(o.columnSize)
                }, {
                    type: "bi.default",
                    tagName: "tbody",
                    ref: (_ref) => {
                        this.layout = _ref;
                    },
                    items: this._formatItems(o.items)
                }]
            }]
        }
    },

    mounted: function () {
        if (this.options.sortable) {
            const draggable = new Draggable(this.table.element[0], {
                draggable: 'tr.enabled',
                handle: ".bi-sticky-table-draggable-handler",
                classes: {
                    "draggable:over": ["bi-sticky-table-draggable-drag-over"],
                    "mirror": ["bi-sticky-table-draggable-mirror"]
                },
                mirror: this.options.mirror
            });

            let lastDragElement;
            let lock = false;
            var lockFn = BI.debounce(function () {
                lock = false;
            }, 300);


            draggable.on("drag:start", (e) => {
                const source = e.data.source;

                // source.style.visibility = "hidden";
                source.style.transform = "";
                source.style.transitionDuration = "";
                this.fireEvent(StickyTableLayout.EVENT_START_DRAG, e.data.source.getAttribute("dataid"));
            });

            draggable.on('drag:stop', (e) => {
                const sourceId = e.data.source.getAttribute("dataid");
                const childNodes = e.data.source.parentElement.childNodes;
                childNodes.forEach((node) => {
                    node.style.visibility = "";
                    node.style.transform = "";
                    node.style.transitionDuration = "";
                    // node.style.pointerEvents = "";
                });
                if(!e.data.sourceContainer.contains(e.data.originalSource)) {
                    e.data.originalSource.remove();
                    e.data.source.remove();
                }

                if (lastDragElement) {
                    this.fireEvent(StickyTableLayout.EVENT_STOP_DRAG, [sourceId, lastDragElement.getAttribute("dataid")]);
                }
                lastDragElement = null;
            });
            draggable.on("drag:over", (e) => {
                const over = e.data.over;
                const source = e.data.source;

                source.style.visibility = "hidden";
                if (over === source || over === e.data.originalSource) {
                    return;
                }
                if (over === lastDragElement) {
                    if (lock) {
                        return;
                    }
                }
                lock = true;
                lockFn();
                if (over.style.transform) {
                    over.style.transform = "";
                    const filterNodes = getFilterNodes();
                    if (lastDragElement === over) {
                        if (filterNodes[0] === over) {
                            lastDragElement = over.nextSibling;
                        } else {
                            lastDragElement = over.previousSibling;
                        }
                        if (!lastDragElement) {
                            lastDragElement = over;
                        }
                        return;
                    }
                    lastDragElement = over;
                    return;
                }

                lastDragElement = over;

                function getFilterNodes () {
                    const childNodes = over.parentElement.childNodes;
                    let startPush = false;

                    // sourceover
                    const filterNodes = [];
                    childNodes.forEach((node) => {
                        if (startPush === true && (node === source || node === over)) {
                            startPush = false;
                            filterNodes.push(node);
                            return;
                        }
                        if (node === source || node === over) {
                            startPush = true;
                        }
                        if (startPush) {
                            filterNodes.push(node);
                        } else {
                            node.style.transform = "";
                        }
                    });
                    return filterNodes;
                }

                const filterNodes = getFilterNodes();
                let transform = "translateY(-100%)";
                if (filterNodes[0] === over) {
                    transform = "translateY(100%)";
                }
                filterNodes.forEach((node) => {
                    if (node === source) {
                        return;
                    }
                    node.style.transform = transform;
                    node.style.transitionDuration = "300ms";
                    // node.style.pointerEvents = "none";
                })
            });
            draggable.on("mirror:created", (e) => {
                const o = this.options;
            })
        }
    },

    _formatCol: function (columnSize) {
        return columnSize.map((width) => {
            return {
                type: "bi.default",
                tagName: "col",
                width
            }
        })
    },

    _formatItems: function (items) {
        var o = this.options;
        return items.map((item, i) => {
            return {
                isNeedFreeze: o.isNeedFreeze,
                draggable: o.sortable,
                checkable: o.checkable,
                freezeCols: o.freezeCols,
                columnSize: o.columnSize,
                minColumnSize: o.minColumnSize,
                maxColumnSize: o.maxColumnSize,
                attributes: {
                    dataid: item.id
                },
                ...item,
                cls: `${item.cls || ""} ${i % 2 ? "even" : "odd"}`
            }
        })
    },

    _populate: function (items) {
        this.layout.populate(this._formatItems(items));
    },

    setColumnSize: function (columnSize) {
        var o = this.options;
        o.columnSize = columnSize;
        this.colgroup.populate(this._formatCol(o.columnSize));
        this.table.setWidth(BI.sum(o.columnSize));
    },

    addItems: function (items) {
        this.layout.addItems(this._formatItems(items));
    },

    populate: function (items) {
        this._populate(items);
    }
});
StickyTableLayout.EVENT_START_DRAG = "EVENT_START_DRAG";
StickyTableLayout.EVENT_STOP_DRAG = "EVENT_STOP_DRAG";
BI.shortcut("bi.sticky_table_layout", StickyTableLayout);

export {StickyTableLayout};
