import { model, Model } from "../../core/decorator";
import { getFunctionTypeByFunctionName, transformFormulaJSONs2FunctionNames, getFunctionDescriptionByFunctionName, getSearchedFunctionNames, getVarDescriptionByVarName } from "./formula.service";
import { DEFAULT_FUNCTION_NAME, DEFAULT_FUNCTION_TYPE } from "./formula.constants";

@model()
export class FormulaModel extends Model {
    static xtype = "bi.model.formula_operating_panel";

    state() {
        return {
            selectedFunctionType: getFunctionTypeByFunctionName(DEFAULT_FUNCTION_NAME) || DEFAULT_FUNCTION_TYPE,
            selectedFunctionNameOrVarName: DEFAULT_FUNCTION_NAME,
            keyword: "",
            needScrollToView: true,
        };
    }

    computed = {
        functionNames: () => transformFormulaJSONs2FunctionNames(this.model.selectedFunctionType),

        functionDescription: () => getFunctionDescriptionByFunctionName(this.model.selectedFunctionNameOrVarName) || getVarDescriptionByVarName(this.model.selectedFunctionNameOrVarName),

        relatedFunctionNames: () => getSearchedFunctionNames(this.model.keyword),
    }

    actions = {
        setSelectedFunctionType: (val: string): void => {
            this.model.selectedFunctionType = val;

            this.model.selectedFunctionNameOrVarName = (BI.first(transformFormulaJSONs2FunctionNames(this.model.selectedFunctionType)) || {}as {
                text: string;
                value: string;
                title: string;
            }).value;
        },

        setSelectedFunctionName: (_functionName: string, needScrollToView = true): void => {
            const functionName = _functionName.toUpperCase();

            const speculatedFunctionType = getFunctionTypeByFunctionName(functionName);

            if (BI.isNull(speculatedFunctionType)) {
                return;
            }

            this.model.selectedFunctionType = speculatedFunctionType;

            this.model.selectedFunctionNameOrVarName = functionName;

            this.model.needScrollToView = needScrollToView;
        },

        setKeyword: (val: string): void => {
            this.model.keyword = val;
        },

        setSelectedVarName: (val: string): void => {
            this.model.selectedFunctionNameOrVarName = val;
        },
    }
}
