var Item = BI.inherit(BI.BasicButton, {
    _defaultConfig: function () {
        return BI.extend(BI.TreeTextLeafItem.superclass._defaultConfig.apply(this, arguments), {
            extraCls: "bi-list-item-active2",
            id: "",
            pId: "",
            height: 24,
            hgap: 0,
            lgap: 0,
            rgap: 0,
            description: "",
        });
    },

    mounted: function () {
        this.element.hover(() => {
            this.fireEvent("EVENT_HOVER", this.getValue());
        });
    },

    render: function () {
        var self = this, o = this.options;

        return {
            type: "bi.label",
            ref: function (_ref) {
                self.text = _ref;
            },
            cls: "draggable-handle",
            textAlign: "left",
            whiteSpace: "nowrap",
            height: o.height,
            hgap: o.hgap,
            lgap: o.lgap,
            rgap: o.hgap,
            text: o.text,
            value: o.value,
            py: o.py,
            keyword: o.keyword,
        };
    },

    doRedMark: function () {
        this.text.doRedMark.apply(this.text, arguments);
    },

    unRedMark: function () {
        this.text.unRedMark.apply(this.text, arguments);
    },

    doHighLight: function () {
        this.text.doHighLight.apply(this.text, arguments);
    },

    unHighLight: function () {
        this.text.unHighLight.apply(this.text, arguments);
    },

    getId: function () {
        return this.options.id;
    },

    getPId: function () {
        return this.options.pId;
    }
});

BI.shortcut("bi.formula_functions_tree.item", Item);
