import { DEFAULT_CONFIG } from "./stickytable.row"
/**
 * @author guy
 * @version 2.0
 * Created by guy on 2022/9/25
 */
const StickyTableHeader = BI.inherit(BI.Widget, {
    props: {
        baseCls: 'bi-sticky-table-header',
        columnSize: [],
        minColumnSize: [],
        maxColumnSize: [],
        freezeCols: [],
        header: [],
        headerRowSize: "auto",
    },

    render: function () {
        var self = this, o = this.options;
        this.tds = [];
        return {
            type: "bi.sticky_table_layout",
            sortable: false,
            ref: (_ref) => {
                this.layout = _ref;
            },
            columnSize: o.columnSize,
            freezeCols: o.freezeCols,
            items: this._formatItems(o.header)
        };
    },

    _formatItems: function (items) {
        var o = this.options;
        return items.map((tds) => {
            return {
                type: "bi.adaptive",
                tagName: "tr",
                height: o.headerRowSize,
                items: tds.map((item, i) => {
                    return {
                        type: "bi.default",
                        tagName: "td",
                        ref: (_ref) => {
                            this.tds[i] = _ref;
                        },
                        extraCls: (o.freezeCols.includes(i) ? (i === tds.length - 1 ? "sticky-table-fix-last-col" : "sticky-table-fix-first-col"): ""),
                        cls: `sticky-table-fix-index-${i}-col first-row ${i === 0 ? "first-col" : ""}`,
                        css: {
                            minWidth: o.minColumnSize[i],
                            maxWidth: o.maxColumnSize[i]
                        },
                        items: [{
                            ...DEFAULT_CONFIG,
                            ...item
                        }]
                    }
                })
            }
        });
    },

    _populate: function (items) {
        this.layout.populate(this._formatItems(items));
    },

    setColumnSize: function (columnSize) {
        this.layout.setColumnSize(columnSize);
    },

    populate: function (items) {
        this._populate(items);
    },

    getRealColumnSize: function () {
        return this.tds.map(td => {
            return td.element.width();
        })
    }
});

BI.shortcut("bi.sticky_table_header", StickyTableHeader);