!(function () {
    var SelectColorComboModel = BI.inherit(Fix.Model, {

        state: function () {
            return {
                selectedColor: this.options.value
            };
        },

        actions: {
            setSelectedColor: function (v) {
                this.model.selectedColor = v;
            }
        }

    });
    BI.model("bi.model.select.color.combo", SelectColorComboModel);
})();