BI.RichEditorFontChooser = BI.inherit(BI.RichEditorAction, {
    _defaultConfig: function () {
        return BI.extend(
            BI.RichEditorFontChooser.superclass._defaultConfig.apply(
                this,
                arguments
            ), {
                baseCls: "bi-rich-editor-font-chooser bi-card",
                command: "FontName",
                width: 100,
                height: 24,
                css: {
                    fontFamily: null
                },
                $testId: "bi-rich-editor-font-chooser",
                simple: false
            }
        );
    },

    _init: function () {
        BI.RichEditorSizeChooser.superclass._init.apply(this, arguments);
        var self = this,
            o = this.options;
        this.trigger = BI.createWidget({
            type: "bi.text_trigger",
            height: o.height,
            triggerWidth: 16,
            text: BI.i18nText("BI-Basic_Default")
        });

        this.combo = BI.createWidget({
            type: "bi.combo",
            cls: o.simple ? 'bi-border-bottom' : 'bi-border',
            container: null,
            element: this,
            el: this.trigger,
            adjustLength: 1,
            popup: {
                minWidth: 70,
                el: {
                    type: "bi.button_group",
                    items: BI.createItems(
                        self._formatArray(
                            BI.isNotEmptyArray(o.fontList)
                                ? o.fontList
                                : BI.Constants.getConstant('bi.constant.materials.rich_editor.font_family')
                        ),
                        {
                            type: "bi.single_select_item",
                        }
                    ),
                    layouts: [{
                        type: "bi.vertical"
                    }]
                }
            }
        });
        this.combo.on(BI.Combo.EVENT_CHANGE, function () {
            var val = this.getValue()[0];
            self.activate(val);
            self.doCommand(val);
            this.hideView();
            this.setValue([]);
        });
    },

    _formatArray: function (arr) {
        return BI.map(arr, function (i, item) {
            return {
                text: item.text,
                value: BI.isString(item.value) ? item.value.replaceAll("\"","") : ""
            };
        });
    },

    hideIf: function (e) {
        if (!this.combo.element.find(e.target).length > 0) {
            this.combo.hideView();
        }
    },

    activate: function (fontFamily) {
        var o = this.options;
        // CHART-19802 
        var map = {
            'STHeiti, Hei, SimHei': 'STHeiti, Hei, SimHei, ',
            'STSong, Song, SimSun': 'STSong, Song, SimSun, ',
            'STFangSong, "Fang Song", FangSong': 'STFangSong, "Fang Song", FangSong, ',
            'STKaiti, Kai, KaiTi': 'STKaiti, Kai, KaiTi, ',
        };
        fontFamily = map[fontFamily] || fontFamily;
        var text = BI.i18nText("BI-Basic_Default");
        BI.any(o.fontList || BI.Constants.getConstant("bi.constant.materials.rich_editor.font_family"), function (idx, item) {
            // \"
            if (fontFamily.replaceAll("\"","") === item.value.replaceAll("\"","")) {
                text = item.text;
                return true;
            }
        });
        this.fontFamily = fontFamily;
        this.trigger.setText(text);
        this.combo.setValue(fontFamily.replaceAll("\"",""));
    },

    deactivate: function () {
        this.trigger.setText(BI.i18nText("BI-Basic_Default"));
        this.combo.setValue();
    },

    getValue: function () {
        return this.fontFamily || BI.FontFamily.DEFAULT;
    }
});
BI.shortcut("bi.rich_editor_font_chooser", BI.RichEditorFontChooser);

// Mac OSSongST Song
BI.FontFamily = {
    DEFAULT: 'Avenir, Segoe UI, PingFangSC-Regular, "Microsoft YaHei"',
    SIM_HEI: "STHeiti, Hei, SimHei, ",
    SIM_SUN: "STSong, Song, SimSun, ",
    FANG_SONG: 'STFangSong, "Fang Song", FangSong, ',
    KAI_TI: "STKaiti, Kai, KaiTi, ",
    YOU_SHE: "YouShe",
    PANG_MEM: "pangMen",
    ARIAL: "Arial",
    HELVETICA_NEUE: '"Helvetica Neue"',
    VERDANA: "Verdana",
    TIMES_NEW_ROMAN: '"Times New Roman"',
    DIGITAL_NUMBERS: '"Digital Numbers"',
    POPPINS_BOLD: '"Poppins Bold"',
};
