/**
 * ------------------------------------------------------------------
 * 
 * @param    {array}  value     
 * @param    {array}  colors    
 * ------------------------------------------------------------------
 */
!(function () {

    var SelectColorTrigger = BI.inherit(BI.Trigger, {

        props: {
            extraCls: " bi-select-color-trigger bi-border",
            height: 28,
            value: []
        },

        mounted: function () {
            var self = this, o = this.options;
            BI.each(this.layouts, function (i, layout) {
                layout.element.css("background-color", o.value[i]);
            });
        },

        render: function () {
            var self = this, o = this.options;
            this.layouts = [];

            return {
                type: "bi.absolute",
                items: [{
                    el: {
                        type: "bi.button_group",
                        items: BI.map(o.value.slice(0, 5), function () {
                            return {
                                type: "bi.layout",
                                ref: function (ref) {
                                    self.layouts.push(ref);
                                }
                            };
                        }),
                        layouts: [{
                            type: "bi.center",
                            hgap: 0.5,
                            vgap: 2
                        }],
                        ref: function (ref) {
                            self.colorContainer = ref;
                        }
                    },
                    left: 1,
                    right: 2,
                    top: 0,
                    bottom: 0
                }, {
                    el: {
                        type: "bi.icon_button",
                        disableSelected: true,
                        cls: "icon-combo-down-icon trigger-triangle-font",
                        width: 15,
                        height: 10,
                        ref: function (ref) {
                            self.down = ref;
                        }
                    },
                    right: 4,
                    bottom: 5
                }]
            };
        },

        populate: function (items) {
            this.options.items = items;
        },

        setValue: function (vals) {
            this.value = vals;
            BI.each(this.layouts, function (i, layout) {
                layout.element.css("background-color", vals[i]);
            });
        },

        getValue: function () {
            return this.value;
        }
    });
    SelectColorTrigger.EVENT_CHANGE = "EVENT_CHANGE";
    BI.shortcut("bi.select_color_trigger", SelectColorTrigger);
})();