import { shortcut, store } from "../../core/decorator";
import { ParameterModel } from "./parameter.model";
import { ParameterInput } from "./parameter_input/parameter_input";
import { ParameterHeader } from "./parameter_header/parameter_header";
import { defaultHeaderItems } from "./parameter_header/parameter_header.constant";
import { ButtonGroup } from '@fui/core';
@shortcut()
@store(ParameterModel)
export class ParameterEditor extends BI.Widget {
    static xtype = "bi.parameter_editor";

    props = {
        height: 200,
        title: "",
        headers: defaultHeaderItems,
        value: [] as ParametersType[],
    }

    parameterInputs: ButtonGroup;

    model: ParameterModel["model"]
    store: ParameterModel["store"]

    watch = {
        parameters: () => {
            this.parameterInputs.populate(this.renderParameterInputs());
        },
        selectedId: (id: string) => {
            this.parameterInputs.setValue(id);
        },
    }

    render() {
        this.setDefaultValue();
        
        return {
            type: BI.VTapeLayout.xtype,
            items: [{
                el: this.renderHeader(),
                height: 25,
            }, {
                el: {
                    type: BI.CenterLayout.xtype,
                    cls: "bi-border",
                    items: [{
                        type: BI.Label.xtype,
                        text: BI.i18nText("BI-Basic_Parameter_Name"),
                    }, {
                        type: BI.Label.xtype,
                        cls: "bi-border-left",
                        text: BI.i18nText("BI-Basic_Parameter_Value"),
                    }],
                },
                height: 25,
            }, {
                type: BI.ButtonGroup.xtype,
                cls: "bi-border",
                layouts: [{
                    type: BI.VerticalLayout.xtype,
                }],
                items: this.renderParameterInputs(),
                ref: (_ref: ButtonGroup) => {
                    this.parameterInputs = _ref;
                },
            }],
        };
    }

    private renderHeader() {
        const { title, headers } = this.options;
        
        return {
            type: ParameterHeader.xtype,
            title,
            headers,
            listeners: [{
                eventName: "EVENT_INSERT",
                action: () => {
                    this.addItem({
                        name: "",
                        value: "",
                        id: BI.UUID(),
                        type: "String",
                    });
                },
            }, {
                eventName: "EVENT_DELETE",
                action: () => {
                    this.removeSelectedItem();
                },
            }, {
                eventName: "EVENT_MOVE_UP",
                action: () => {
                    this.moveUp();
                },
            }, {
                eventName: "EVENT_MOVE_DOWN",
                action: () => {
                    this.moveDown();
                },
            }, {
                eventName: "EVENT_REFRESH",
                action: () => {
                    this.refresh();
                    this.fireEvent("EVENT_REFRESH");
                },
            }],
        };
    }

    public getValue() {
        return this.model.parameters.map(item => {
            return {
                name: item.name,
                value: item.value,
                type: item.type,
            };
        });
    }

    public setValue(value: ParametersType[]) {
        this.store.setParameters(value.map(item => {
            return {
                ...item,
                id: BI.UUID(),
            };
        }));
    }

    public addItem(item: ParametersType) {
        const { name = "", value = "", type = "String" } = item;
        this.store.setParameters([...this.model.parameters, {
            name,
            value,
            type,
            id: BI.UUID(),
        }]);
    }

    public removeSelectedItem() {
        if (this.model.selectedId) {
            BI.Msg.confirm(BI.i18nText("BI-Basic_Prompt"), BI.i18nText("BI-Basic_Parameter_Delete_Confirm"), (re: boolean) => {
                if (re) {
                    this.store.removeSelectedParameter();
                }
            });
        } else {
            BI.Msg.alert(BI.i18nText("BI-Basic_Prompt"), BI.i18nText("BI-Basic_Parameter_Delete_Alert"));
        }
    }

    public moveUp() {
        this.store.move("up");
    }

    public moveDown() {
        this.store.move("down");
    }

    public refresh() {
        this.setDefaultValue();
    }

    private setDefaultValue() {
        const { value = [] } = this.options;
        this.setValue(value);
    }

    private renderParameterInputs() {
        return this.model.parameters.map(item => {
            return {
                type: ParameterInput.xtype,
                inputName: item.name,
                inputValue: item.value,
                inputType: item.type,
                id: item.id,
                value: item.id,
                selected: item.id === this.model.selectedId,
                listeners: [{
                    eventName: 'EVENT_CHANGE',
                    action: () => {
                        this.fireEvent('EVENT_CHANGE');
                    },
                }],
            };
        });
    }
}


export interface ParametersType {
    name: string;
    value: string;
    id?: string;
    type: inputTypes;
}

export type inputTypes = "String" | "Integer" | "Double" | "Date" | "Boolean" | "Formula"
