import { tokenMatcher } from "chevrotain";
import { token as tokenMap } from "./token";
import forEach from "lodash/forEach";
import isArray from "lodash/isArray";
import keys from "lodash/keys";
import findIndex from "lodash/findIndex";
import get from "lodash/get";
import getFormulaParser from "./FormulaParser";
// import FunctionCheck from "./FunctionCheck";

/**
 * Tokens
 *
 * @param tokensList
 */
const concatTokens = (...tokensList) => {
  let tokens = [];
  forEach(tokensList, function(item) {
    if (item) {
      tokens = tokens.concat(item);
    }
  });
  return tokens;
};

/**
 * tokens
 *
 * @param tokens
 */
const sortTokensByPosition = tokens => {
  const result = [].concat(tokens);
  return result.sort(function(a, b) {
    return a.startOffset - b.startOffset;
  });
};

/**
 * 
 *
 * @param op
 * @param left
 * @param right
 */
const transformType = (op, left, right) => {
  switch (op) {
    case "+":
      // 1
      // 2
      if (isStringType(left) || isStringType(right) || (isAnyType(left) && !isNumberType(left)) || (isAnyType(right) && !isNumberType(right))) {
        return "string";
      }
      return "number";
    case "-":
      if (((!isNumberType(left) && !isDateType(left)) || (!isNumberType(right) && !isDateType(right))) && !isAnyType(left) && !isAnyType(right)) {
        throw new Error(
            JSON.stringify({
              type: "visit",
              token: op,
              errorType: "operation",
              errorText: BI.i18nText('BI-Conf_Formula_Valid'),
            })
        );
      }
      return "number";
    case "*":
    case "/":
      if ((!isNumberType(left) || !isNumberType(right)) && !isAnyType(left) && !isAnyType(right)) {
        throw new Error(
            JSON.stringify({
              type: "visit",
              token: op,
              errorType: "operation",
              errorText: BI.i18nText('BI-Conf_Formula_Valid'),
            })
        );
      }
      return "number";
  }
};

/**
 * token
 *
 * @param sortedTokens
 * @param operands
 * @param consumeToken
 * @param sortedTokens
 * @param operands
 * @param consumeToken
 * @param sortedTokens
 * @param operands
 * @param consumeToken
 */
const consumeBinaryToken = (sortedTokens, operands, consumeToken) => {
  const resultTokens = sortedTokens.slice();
  const resultOperands = operands.slice();
  const tokenNames = sortedTokens.map(token => {
    return token.image;
  });
  const index = tokenNames.indexOf(consumeToken.image);
  if (index > -1) {
    const tempOperands = resultOperands.slice().splice(index, 2);
    const left = tempOperands[0];
    const right = tempOperands[1];
    let result = "null";
    if (
      tokenMatcher(consumeToken, tokenMap.Addition) ||
      tokenMatcher(consumeToken, tokenMap.Multiplication)
    ) {
      result = transformType(consumeToken.image, left, right);
    } else if (tokenMatcher(consumeToken, tokenMap.Compare)) {
      if (left !== null && right !== null && getType(left) !== getType(right)) {
        throw new Error(
            JSON.stringify({
              type: "visit",
              token: consumeToken.image,
              errorType: "compare",
              errorText: BI.i18nText('BI-Basic_Formula_Invalid'),
            })
        );
      }
      result = "boolean";
    } else if (tokenMatcher(consumeToken, tokenMap.Equal)) {
      result = "boolean";
    } else if (tokenMatcher(consumeToken, tokenMap.BitAnd)) {
      result = "string";
    // } else if (tokenMatcher(consumeToken, tokenMap.BitOr)) {
    //   result = "number";
    } else if (tokenMatcher(consumeToken, tokenMap.LogicAnd) || tokenMatcher(consumeToken, tokenMap.LogicOr)) {
      if (!(isNumberType(left) && isNumberType(right) || isBooleanType(left) && isBooleanType(right))) {
        throw new Error(
            JSON.stringify({
              type: "visit",
              token: consumeToken.image,
              errorType: "logic",
              errorText: BI.i18nText('BI-Basic_Formula_Invalid'),
            })
        );
      }
      result = left || right;
    }
    resultTokens.splice(index, 1);
    resultOperands.splice(index, 2, result);
  }
  return {
    resultTokens: resultTokens,
    resultOperands: resultOperands
  };
};

/**
 * 
 */
const getFormulaInterpreter = () => {
  const parser = getFormulaParser();
  const BaseCstVisitorConstructor = parser.getBaseCstVisitorConstructor();
  let formula;

  const setFormula = f => {
    formula = f;
  };

  const applyFunc = (func, args) => {
    if (!formula) {
      throw new Error("Interpreter is not set formula");
    }
    const funcKeys = keys(formula);
    if (findIndex(funcKeys, k => k === func) > -1) {
      const f = get(formula, func);
      return f.apply(this, args);
    }
    return null;
  };

  const FormulaVisitor = class extends BaseCstVisitorConstructor {
    constructor() {
      super();
      this.validateVisitor();
    }

    expr(ctx) {
      return this.visit(ctx.commaExpression);
    }

    commaExpression(ctx) {
      const lastCstNode = ctx.binaryExpression.slice(-1)[0];
      return this.visit(lastCstNode);
    }

    binaryExpression(ctx) {
      const binaryTokens = concatTokens(
        ctx.Multiplication,
        ctx.Addition,
        ctx.Compare,
        ctx.Equal,
        ctx.BitAnd,
        // ctx.BitOr,
        ctx.LogicAnd,
        ctx.LogicOr
      );
      let tempTokens = sortTokensByPosition(binaryTokens);
      let tempOperands = [];
      forEach(ctx.unaryExpression, e => {
        tempOperands.push(this.visit(e));
      });
      forEach(binaryTokens, token => {
        const ret = consumeBinaryToken(tempTokens, tempOperands, token);
        tempTokens = ret.resultTokens;
        tempOperands = ret.resultOperands;
      });
      return tempOperands[0];
    }

    unaryExpression(ctx) {
      let result = this.visit(ctx.atomicExpression);
      if (!ctx.Unary) {
        return result;
      }
      const signs = ctx.Unary.slice().reverse();
      forEach(signs, sign => {
        if (tokenMatcher(sign, tokenMap.Minus)) {
          result = "number";
        } else if (tokenMatcher(sign, tokenMap.Plus)) {
          result = "number";
        } else {
          result = "boolean";
        }
      });
      return result;
    }

    atomicExpression(ctx) {
      let result;
      if (ctx.base) {
        result = this.visit(ctx.base);
      } else if (ctx.group) {
        result = this.visit(ctx.group);
      } else if (ctx.array) {
        result = this.visit(ctx.array);
      } else if (ctx.func) {
        result = this.visit(ctx.func);
      }
      if (!ctx.expr) {
        return result;
      } else {
        forEach(ctx.expr, e => {
          const index = this.visit(e);
          result = result[index];
        });
        return result;
      }
    }

    array(ctx) {
      const result = [];
      forEach(ctx.binaryExpression, e => {
        result.push(this.visit(e));
      });
      return result;
    }

    func(ctx) {
      const func = ctx.Func[0].image;
      const args = [];
      forEach(ctx.binaryExpression, e => {
        args.push(this.visit(e));
      });
      return applyFunc(func, args);
    }

    group(ctx) {
      return this.visit(ctx.expr);
    }

    base(ctx) {
      if (ctx.String) {
        // 
        let str = ctx.String[0].image;
        if (str !== null) {
          return `string:${str}`;
        }
        return "string";
      }
      if (ctx.Number) {
        let num = ctx.Number[0].image;
        if (num !== null) {
          return `number:${num}`;
        }
        return "number";
      }
      if (ctx.Date) {
        let date = ctx.Date[0].image;
        if (date !== null) {
          return `date:${date}`;
        }
        return "date";
      }
      if (ctx.Boolean) {
        let bol = ctx.Boolean[0].image;
        if (bol !== null) {
          return `boolean:${bol}`;
        }
        return "boolean";
      }
      if (ctx.Empty) {
        return "null";
      }
      if (ctx.NaN) {
        return "number";
      }
    }
  };

  return {
    interpreter: new FormulaVisitor(),
    setFormula
  };
};


/**
 * 
 *
 * @param param
 * @returns {boolean}
 */
export const isNumberType = param => {
  return param === null || BI.startWith(param, "number") || BI.startWith(param, "boolean");
};

/**
 * 
 *
 * @param param
 * @returns {boolean}
 */
export const isStringType = param => {
  return param === null || BI.startWith(param, "string");
};

/**
 * 
 *
 * @param param
 * @returns {boolean}
 */
export const isBooleanType = param => {
  return param === null || BI.startWith(param, "boolean");
};

/**
 * 
 *
 * @param param
 * @returns {boolean}
 */
export const isArrayType = param => {
  return isArray(param) || param === null;
};

/**
 * 
 *
 * @param param
 * @returns {boolean}
 */
export const isDateType = param => {
  return param === null || BI.startWith(param, "date");
};

/**
 * IF
 * 2022-9-28null, 
 *
 * @param param
 * @returns {boolean}
 */
export const isAnyType = param => {
  return param === "any" || param === null;
};

const getType = param => {
  if (isStringType(param)) {
    return 'string';
  }
  if (isNumberType(param)) {
    return 'number';
  }
  if (isDateType(param)) {
    return 'date';
  }
  if (isBooleanType(param)) {
    return 'boolean';
  }
  if (isArrayType(param)) {
    return 'array';
  }
  if (isAnyType(param)) {
    return 'any';
  }

  return undefined;
};

export default getFormulaInterpreter;
