import { shortcut } from "../../../core/decorator";
import * as CodeMirror from "codemirror";
import { getVarDisplayNameByValue, isBelong2FormulaFunction } from "../formula.service";

const paramRegex = /(\$[{][^}]*[}])/;

@shortcut()
export class FormulaInserter extends BI.Widget {
    static xtype = "bi.formula_operating_panel.inserter";

    protected getToolbarLayout(): {[key: string]: any;} {
        return {
            type: BI.Layout.xtype,
        };
    }

    protected formulaEditor: any;

    props = {
        value: {
            formula: "",
        },
    }

    render() {
        return {
            type: BI.VTapeLayout.xtype,
            items: [{
                el: {
                    type: BI.FormulaEditor.xtype,
                    cls: "bi-border",
                    ref: (_ref: any) => {
                        this.formulaEditor = _ref;
                    },
                    watermark: BI.i18nText("BI-Basic_Formula_Watermark"),
                    listeners: [{
                        eventName: BI.FormulaEditor.EVENT_CHANGE,
                        action: () => {
                            this.validate();
                        },
                    }, {
                        eventName: "EVENT_SHOW_HINT",
                        action: (list: string[]) => {
                            this.fireEvent("EVENT_SHOW_HINT", BI.first(list));
                        },
                    }, {
                        eventName: "EVENT_SELECT_HINT",
                        action: (hint: string) => {
                            this.fireEvent("EVENT_SELECT_HINT", hint);
                        },
                    }],
                },
                height: 155,
            }, this.getToolbarLayout()],
        };
    }

    mounted() {
        this.setValue(this.options.value.formula);
    }

    /**
     * function
     * @param val function
     */
    insertFunction(val: string) {
        this.formulaEditor.insertFunction(val);
    }

    /**
     * 
     * @param val 
     */
    insertField(val: string) {
        this.formulaEditor.insertField(val, true);
    }

    getValue() {
        return {
            formula: this.formulaEditor.getValue() as string,
        };
    }

    /**
     * 
     */
    validate(): void {
        const val: string = this.formulaEditor.getValue();

        if (val === "") {
            this.formulaEditor.hideTip();

            return;
        }

        this.fireEvent("EVENT_VALIDATE");
    }

    setValidation(data: {
        validation: boolean;
    }) {
        data.validation ? this.formulaEditor.hideTip() : this.formulaEditor.showTip(BI.i18nText("BI-Basic_Formula_Invalid"));
    }

    setValue(value: string) {
        this.formulaEditor.refresh();
            
        this.formulaEditor.setValue("");

        this.insertFormula(value);

        this.validate();
    }

    /**
     * 
     * @param v 
     */
    private analyzeContent(v: string) {
        // \w*\w , \w ? \w+
        const regx = /\$[{][^}]*[}]|\w+|\$\{[^$()+\-*/)$,]*\w\}|\$\{[^$()+\-*/]*\w\}|\$\{[^$()+\-*/]*[\u4e00-\u9fa5]\}|\w|(.)|\n/g;

        return v.match(regx);
    }

    /**
     * 
     * @param v 
     */
    private paramFormatter(v: string) {
        return getVarDisplayNameByValue(v);
    }

    /**
     * 
     * @param value 
     * @param position 
     */
    private insertFormula(value: string = "", position?: CodeMirror.Position) {
        const result = this.analyzeContent(value) || [];

        const fieldArray: {
            from: CodeMirror.Position,
            to: CodeMirror.Position,
            value: string,
            invalid: boolean,
            text: string,
        }[] = [], functionArray: {
            from: CodeMirror.Position,
            to: CodeMirror.Position,
            value: string,
        }[] = [];

        let lineNum = 0, offset = 0, valueString = "";

        if (BI.isNotNull(position)) {
            lineNum = position.line;
            offset = position.ch;
        }

        const wrapChar: string = this.formulaEditor.getParamWrap();

        result.forEach(str => {
            if (paramRegex.test(str)) {
                // 
                const fieldId = str.substring(2, str.length - 1);

                const formattedParam = this.paramFormatter(fieldId);

                const fieldName = wrapChar + formattedParam.replaceAll("^<!.*!>$", str => str.substring(2, str.length - 2)) + wrapChar;

                const fromPos = CodeMirror.Pos(lineNum, offset);

                valueString += fieldName;

                offset += fieldName.length;

                const endPos = CodeMirror.Pos(lineNum, offset);
                fieldArray.push({
                    from: fromPos,
                    to: endPos,
                    value: fieldId,
                    invalid: /^<!.*!>$/.test(formattedParam),
                    text: fieldName,
                });
            } else {
                if (BI.isNotNull(isBelong2FormulaFunction(str))) {
                    const fromPos = CodeMirror.Pos(lineNum, offset);

                    const endPos = CodeMirror.Pos(lineNum, offset + str.length);

                    functionArray.push({
                        from: fromPos,
                        to: endPos,
                        value: str,
                    });
                }

                if (str === "\n") {
                    lineNum++;

                    offset = 0;
                }
                valueString += str;
                str !== "\n" && (offset += str.length);
            }
        });

        this.formulaEditor.insertString(valueString);

        BI.each(fieldArray, (_idx, field) => {
            this.formulaEditor.markField(field);
        });

        BI.each(functionArray, (_idx, field) => {
            this.formulaEditor.markFunction(field);
        });
    }
}
