/**
 * 
 *
 * @param config.name 
 * @param config.params 
 * @param config.checkFunction 
 * @param config.paramCount 
 * @param config.minCount 
 * @param config.paramType 
 * @param config.optionalCount 
 * @param config.isSupportDynamic 
 * @param config.errorText 
 * @param config
 */
const checkParamsType = config => {
    if (!isUndefined(config.paramCount) && size(config.params) !== config.paramCount
        || !isUndefined(config.minCount) && size(config.params) < config.minCount
        || !isUndefined(config.minCount) && !isUndefined(config.optionalCount) && size(config.params) > config.minCount + config.optionalCount) {
        throw new Error(
            JSON.stringify({
                type: "function",
                name: config.name,
                paramCount: config.paramCount,
                minCount: config.minCount,
                optionalCount: config.optionalCount,
                errorType: "paramCount",
                errorText: config.errorText,
            })
        );
    }
    // 
    const needCheckCount = config.minCount + (config.optionalCount || 0);
    // 
    const supportDynamic = !!config.isSupportDynamic;
    forEach(config.params, (p, i) => {
        if (i + 1 > needCheckCount && supportDynamic) {
            return false;
        }
        if (!config.checkFunction(p, i, config.params) && !isAnyType(p)) {
            throw new Error(
                JSON.stringify({
                    type: "function",
                    name: config.name,
                    paramIndex: i + 1,
                    paramType: config.paramType,
                    errorType: "paramType",
                    errorText: config.errorText,
                })
            );
        }
    });
};

