!(function () {
    var Widget = BI.inherit(BI.Widget, {
        props: {
            baseCls: "bi-steps-item",
            text: "",
            stepWidth: 88,
            stepHeight: 50,
            showLine: true
        },

        render: function () {
            var self = this,
                o = this.options;

            this.constant = BI.Constants.getConstant("bi.constants.steps.status");
            this.status = this.constant.NEXT;

            return {
                type: "bi.htape",
                height: o.stepHeight,
                items: [{
                    el: {
                        type: "bi.vertical_adapt",
                        items: [{
                            type: "bi.layout",
                            cls: "step-line",
                            height: 2,
                            width: "100%",
                            invisible: !o.showLine
                        }]
                    },
                    rgap: 8
                }, {
                    el: {
                        type: "bi.right_vertical_adapt",
                        items: [{
                            type: "bi.icon_change_button",
                            iconCls: "step-next",
                            iconWidth: 14,
                            iconHeight: 14,
                            ref: function (_ref) {
                                self.icon = _ref;
                            },
                            rgap: 8
                        }, {
                            type: "bi.label",
                            cls: "step-text",
                            textAlign: "left",
                            whiteSpace: "nowrap",
                            rgap: 5,
                            text: o.text
                        }]
                    },
                    width: o.stepWidth
                }]
            };
        },

        setStatus: function (status) {
            if (status === this.status) {
                return;
            }
            this.status = status;

            switch (status) {
                case this.constant.CURR:
                    this.icon.setIcon("step-now");
                    this.element.addClass("active");
                    break;
                case this.constant.PREV:
                    this.icon.setIcon("step-prev");
                    this.element.addClass("active");
                    break;
                case this.constant.NEXT:
                default:
                    this.icon.setIcon("step-next");
                    this.element.removeClass("active");
                    break;
            }
        }

    });
    BI.shortcut("bi.steps.item", Widget);
})();