import { shortcut } from "../../../core/decorator";
import { headerItemsData, defaultHeaderItems } from "./parameter_header.constant";
@shortcut()
export class ParameterHeader extends BI.Widget {
    static xtype = "bi.parameter_editor.header";
    props = {
        title: "",
        headers: defaultHeaderItems,
    }

    render() {
        const { title } = this.options;
        
        return {
            type: BI.LeftRightVerticalAdaptLayout.xtype,
            items: {
                left: [{
                    type: BI.Label.xtype,
                    lgap: 2,
                    text: title,
                    textAlign: "left",
                }],
                right: this.renderItem(),
            },
        };
    }

    private renderItem() {
        const { headers } = this.options;
        
        return headers.map(item => {
            if (BI.isString(item)) {
                const head = headerItemsData.find(data => data.type === item);
                if (head) {
                    return {
                        type: BI.IconButton.xtype,
                        cls: head.cls,
                        width: 25,
                        title: BI.i18nText(head.title),
                        handler: () => {
                            this.fireEvent(head.event);
                        },
                    };
                }
                
                return {
                    type: "bi.default",
                };
            }
            
            return item;
        });
    }
}