/**
 * 
 * @class String
 */
_.extend(String.prototype, {

    /**
     * 
     * @param {String} startTag   
     * @return {Boolean}  truefalse
     */
    startWith: function (startTag) {
        return BI.startWith(this, startTag);
    },
    /**
     * 
     * @param {String} endTag 
     * @return {Boolean}  truefalse
     */
    endWith: function (endTag) {
        return BI.endWith(this, endTag);
    },

    /**
     * url
     * @param {String} name 
     * @return {String} 
     */
    getQuery: function (name) {
        return BI.getQuery(this, name);
    },

    /**
     * url
     * @param {Object} paras 
     * @return {String} url
     */
    appendQuery: function (paras) {
        return BI.appendQuery(this, paras);
    },
    /**
     * 
     * @param {String} s1 
     * @param {String} s2 
     * @returns {String} 
     */
    replaceAll: function (s1, s2) {
        return BI.replaceAll(this, s1, s2);
    },
    /**
     * 
     * @param {String} start 
     * @returns {String} 
     */
    perfectStart: function (start) {
        return BI.perfectStart(this, start);
    },

    /**
     * 
     * @param {String} sub 
     * @return {Number[]} 
     */
    allIndexOf: function (sub) {
        return BI.allIndexOf(this, sub);
    }
});