!(function () {
    function AutoScrollHelper(container, onScrollCallback) {
        this.container = container;
        this.onScrollCallback = onScrollCallback;
    }

    AutoScrollHelper.prototype.update = function (config) {
        var offsetX = 0, autoScroll = false;
        var translateX = config.translateX, minTranslateX = config.minTranslateX, maxTranslateX = config.maxTranslateX, width = config.width;
        if (translateX <= minTranslateX + width / 2) {
            offsetX = -10 * Math.abs((translateX - width / 2 - minTranslateX) / width);
            autoScroll = true;
        } else if (translateX >= maxTranslateX - width / 2) {
            offsetX = 10 * Math.abs((maxTranslateX - width / 2 - translateX) / width);
            autoScroll = true;
        }
        this.clear();

        if (autoScroll) {
            this.interval = setInterval(() => {
                var initialScrollLeft = this.container.getActualRightHorizontalScroll();
                this.container.setRightHorizontalScroll(initialScrollLeft + offsetX);
                this.onScrollCallback(offsetX);
                if (this.container.getActualRightHorizontalScroll() === initialScrollLeft) {
                    this.clear();
                }
            }, 5);
        }
    };

    AutoScrollHelper.prototype.clear = function () {
        if (this.interval === null) {
            return;
        }
        clearInterval(this.interval);
        this.interval = null;
    };

    BI.SortableTable.AutoScrollHelper = AutoScrollHelper;
}());
