import { shortcut } from "../../../core/decorator";

@shortcut()
export class FormulaVarItem extends BI.BasicButton {
    static xtype = "bi.formula.var_item";

    private leaf: any;

    props = {
        text: "",
        title: "",
        value: "",
        layoutType: BI.MidTreeLeafItem.xtype,
    };

    render() {
        return {
            type: BI.HTapeLayout.xtype,
            items: [{
                type: BI.Label.xtype,
                width: 16,
            }, {
                type: this.options.layoutType,
                cls: "bi-list-item-select",
                ref: (_ref: any) => {
                    this.leaf = _ref;
                },
                text: this.options.text,
                value: this.options.value,
                trigger: "dblclick",
                textAlign: "left",
                handler: (...args: any[]) => {
                    this.fireEvent(BI.Controller.EVENT_CHANGE, BI.Events.CLICK, ...args);
                },
            }],
        };
    }

    setSelected(flag: boolean) {
        super.setSelected(flag);

        this.leaf.setSelected(flag);
    }
}
