import { shortcut } from "../../../core/decorator";

@shortcut()
export class FormulaFunctionItem extends BI.BasicButton {
    static xtype = "bi.formula.function_item";

    props = {
        baseCls: "bi-list-item-select",
        text: "",
        title: "",
        value: "",
    };

    render() {
        return {
            type: BI.TextButton.xtype,
            text: this.options.text,
            value: this.options.value,
            trigger: "dblclick",
            textAlign: "left",
            hgap: 5,
            handler: (...args: any[]) => {
                this.fireEvent(BI.Controller.EVENT_CHANGE, BI.Events.CLICK, ...args);
            },
        };
    }
}
