import { shortcut } from "../../../../core/decorator";
import { input } from "./input.service";

@shortcut()
@input("Date")
export class InputDate extends BI.Widget {
    static xtype = "bi.parameter_editor.parameter_input.date"

    props = {
        value: "",
    }

    input: any;
    
    render() {
        const { value } = this.options;
        const dates = value.split("-");
        
        return {
            type: BI.DynamicDateCombo.xtype,
            height: 22,
            allowBlank: true,
            value: dates.length === 3 ? {
                year: dates[0],
                month: dates[1],
                day: dates[2],
            } : "",
            ref: (_ref: any) => {
                this.input = _ref;
            },
            listeners: [{
                eventName: BI.DynamicDateCombo.EVENT_CONFIRM,
                action: () => {
                    const { day, month, year } = this.input.getValue().value as {
                            day: number;
                            month: number;
                            year: number;
                    };
                    this.fireEvent("EVENT_CHANGE", `${year}-${month}-${day}`);
                },
            }, {
                eventName: BI.DynamicDateCombo.EVENT_FOCUS,
                action: () => {
                    this.fireEvent("EVENT_FOCUS");
                },
            }],
        };
    }
}
