import { createToken, Lexer } from "chevrotain";

// Token 
const Unary = createToken({ name: "Unary", pattern: Lexer.NA });
const Addition = createToken({ name: "Addition", pattern: Lexer.NA });
const Multiplication = createToken({
  name: "Multiplication",
  pattern: Lexer.NA
});
const Equal = createToken({ name: "Equal", pattern: Lexer.NA });
const Compare = createToken({ name: "Compare", pattern: Lexer.NA });

const token = {
  WhiteSpace: createToken({
    name: "WhiteSpace",
    pattern: /\s+/,
    group: Lexer.SKIPPED
  }),
  // 
  String: createToken({
    name: "String",
    pattern: /("(\\\\|\\"|[^"])*"|'(\\\\|\\'|[^'])*')/
  }),
  Number: createToken({
    name: "Number",
    pattern: /[0-9]+(\.[0-9]+)?(e[+-]?[0-9]+)?/i
  }),
  Date: createToken({
    name: "Date",
    pattern: /("bi-date-place-holder"|'bi-date-place-holder')/
  }),
  Boolean: createToken({
    name: "Boolean",
    pattern: /(true|false)/i
  }),
  Empty: createToken({
    name: "Empty",
    pattern: /(null|undefined)/i
  }),
  NaN: createToken({
    name: "NaN",
    pattern: /NaN/
  }),
  // 
  Comma: createToken({ name: "Comma", pattern: /,/ }),
  Func: createToken({ name: "Func", pattern: /[A-Za-z0-9_.]+/ }),
  LParen: createToken({ name: "LParen", pattern: /\(/ }),
  RParen: createToken({ name: "RParen", pattern: /\)/ }),
  LSquare: createToken({ name: "LSquare", pattern: /\[/ }),
  RSquare: createToken({ name: "RSquare", pattern: /]/ }),
  Plus: createToken({
    name: "Plus",
    pattern: /\+(?!\+)/,
    categories: [Unary, Addition]
  }),
  Minus: createToken({
    name: "Minus",
    pattern: /-(?!-)/,
    categories: [Unary, Addition]
  }),
  Unary: Unary,
  Addition: Addition,
  Multi: createToken({
    name: "Multi",
    pattern: /\*/,
    categories: [Multiplication]
  }),
  Div: createToken({
    name: "Div",
    pattern: /\//,
    categories: [Multiplication]
  }),
  Multiplication: Multiplication,
  LessOrEqual: createToken({
    name: "LessOrEqual",
    pattern: /<=/,
    categories: [Compare]
  }),
  GreaterOrEqual: createToken({
    name: "GreaterOrEqual",
    pattern: />=/,
    categories: [Compare]
  }),
  Less: createToken({ name: "Less", pattern: /</, categories: [Compare] }),
  Greater: createToken({
    name: "Greater",
    pattern: />/,
    categories: [Compare]
  }),
  Compare: Compare,
  NotEqualStrict: createToken({
    name: "NotEqualStrict",
    pattern: /!==/,
    categories: [Equal]
  }),
  EqualLoose: createToken({
    name: "EqualLoose",
    pattern: /==/,
    categories: [Equal]
  }),
  NotEqualLoose: createToken({
    name: "NotEqualLoose",
    pattern: /!=/,
    categories: [Equal]
  }),
  Equal: Equal,
  EqualStrict: createToken({
    name: "EqualStrict",
    pattern: /=/,
    categories: [Equal]
  }),
  LogicNot: createToken({
    name: "LogicNot",
    pattern: /!/,
    categories: [Unary]
  }),
  LogicAnd: createToken({ name: "LogicAnd", pattern: /&&/ }),
  LogicOr: createToken({ name: "LogicOr", pattern: /\|\|/ }),
  BitAnd: createToken({ name: "BitAnd", pattern: /&/ }),
  // BitAnd: createToken({ name: "BitAnd", pattern: /&/ }),
  // BitOr: createToken({ name: "BitOr", pattern: /\|/ })
};

const allTokens = [
  token.WhiteSpace,
  token.Date,
  token.String,
  token.Number,
  token.NaN,
  token.Boolean,
  token.Empty,
  token.Func,
  token.LParen,
  token.RParen,
  token.LSquare,
  token.RSquare,
  token.Plus,
  token.Minus,
  token.Addition,
  token.Unary,
  token.Multi,
  token.Div,
  token.Multiplication,
  token.LessOrEqual,
  token.GreaterOrEqual,
  token.Less,
  token.Greater,
  token.Compare,
  token.NotEqualStrict,
  token.EqualLoose,
  token.NotEqualLoose,
  token.Equal,
  token.EqualStrict,
  token.LogicNot,
  token.LogicAnd,
  token.LogicOr,
  token.BitAnd,
  // token.BitOr,
  token.Comma
];

export { token, allTokens };
