/**
 * ------------------------------------------------------------------
 * 
 * @param    {String}  header     
 * @param    {Array}   value      
 * @param    {Array}   colors     
 * ------------------------------------------------------------------
 */
!(function () {
    var SelectColorItemWrapper = BI.inherit(BI.BasicButton, {
        props: {
            extraCls: "bi-select-color-item-wrapper",
            header: "",
            value: [],
            colors: [],
            height: 50,
            width: 130
        },

        render: function () {
            var self = this, o = this.options;
            return {
                type: "bi.vtape",
                hgap: 2,
                items: [{
                    type: "bi.label",
                    textAlign: "left",
                    text: o.header,
                    lgap: 3
                }, {
                    el: {
                        type: "bi.select_color_item",
                        value: o.value,
                        colors: o.colors,
                        ref: function (ref) {
                            self.item = ref;
                        },
                        listeners: [{
                            eventName: "EVENT_CHANGE",
                            action: function () {
                                self.fireEvent(BI.Controller.EVENT_CHANGE, this.getValue());
                            }
                        }]
                    },
                    height: 28
                }]
            };
        },

        setSelected: function (v) {
            this.item.setSelected(v);
        },

        isSelected: function () {
            return this.item.isSelected();
        }
    });
    BI.shortcut("bi.select_color_item_wrapper", SelectColorItemWrapper);
})();