BI.WarningLabel = BI.inherit(BI.Single, {

    props: {
        baseCls: "bi-warning-label",
        textAlign: "center",
        whiteSpace: "nowrap", // normal  or  nowrap
        forceCenter: false, // , , 
        textWidth: null,
        textHeight: null,
        hgap: 0,
        vgap: 0,
        lgap: 0,
        rgap: 0,
        tgap: 0,
        bgap: 0,
        text: ""
    },


    render: function () {
        var self = this, o = this.options;
        return {
            type: "bi.label",
            ref: function () {
                self.label = this;
            },
            textAlign: o.textAlign,
            whiteSpace: o.whiteSpace, // normal  or  nowrap
            forceCenter: o.forceCenter, // , , 
            textWidth: o.textWidth,
            textHeight: o.textHeight,
            hgap: o.hgap,
            vgap: o.vgap,
            lgap: o.lgap,
            rgap: o.rgap,
            tgap: o.tgap,
            bgap: o.bgap
        };
    },

    mounted: function () {
        var o = this.options;
        if(BI.isKey(o.text)) {
            this.setText(o.text);
        }
    },

    _digest: function (text) {
        text = BI.htmlEncode(text || "");
        return text.replaceAll("\\$\\{(.*?)\\}", function (ori, str) {
            return "<span class='warning-label'>" + str + "</span>";
        });
    },

    setText: function (v) {
        this.options.text = v;
        this.label.text.text.element.html(this._digest(v));
    },

    getText: function () {
        return this.options.text;
    },

    setValue: function (v) {
        this.label.setValue(v);
    }
});
BI.shortcut("bi.warning_label", BI.WarningLabel);